import { ApiParams } from '../Components';

interface ModelAttributes<T> {
  set(value: T): void;

  getAll(): T;

  get<K extends keyof T>(key: K): T[K];
}

// interface Sync {
//   fetch(apiParams: ApiParams): AxiosPromise;
// }

interface Events {
  on(eventName: string, callback: () => void): void;

  trigger(eventName: string): void;
}

export class ApiModel<T extends ApiParams> {
  constructor(
    public attributes: ModelAttributes<T>,
    private events: Events
  ) // private sync: Sync
  {
    this.getApiParams = this.getApiParams.bind(this);
  }

  on = this.events.on;
  trigger = this.events.trigger;

  get = this.attributes.get;
  getAll = this.attributes.getAll;

  // set(update: T): void {
  //   this.attributes.set(update);
  // }

  getApiParams(): ApiParams {
    return {
      apiReturnType: this.get('apiReturnType'),
      authParams: this.get('authParams'),
      queryParams: this.get('queryParams'),
      path: this.get('path'),
      apiReturnValue: this.get('apiReturnValue'),
      apiBoostedFields: this.get('apiBoostedFields')
    };
  }

  // fetch(): void {
  //   this.sync.fetch(this.getApiParams()).then(
  //     (response: AxiosResponse): void => {
  //       this.set(response.data);
  //     }
  //   );
  // }
}
