import axios, { AxiosResponse } from 'axios';
import { ApiParams, AppFunctions } from '../Components';
import { Eventing } from './Eventing';
import * as https from 'https';

const httpsAgent = new https.Agent({ keepAlive: true, maxSockets: 40, maxFreeSockets: 10, timeout: 900000 });

const getBasicAuth = (adminName: string, password: string) => {
  let buff = new Buffer(adminName + ':' + password);
  return buff.toString('base64');
};

const serialize = (obj) => {
  let str = [];
  for (let p in obj)
    if (obj.hasOwnProperty(p)) {
      str.push(encodeURIComponent(p) + '=' + encodeURIComponent(obj[p]));
    }
  return str.join('&');
};

export class ApiCollection<T, K> {
  models: T[] = [];
  events: Eventing = new Eventing();
  page = 0;
  pageSize = 0;
  total = 0;
  numberOfPages = 0;
  
  constructor(
    public apiParams: ApiParams,
    public deserialize: (json: K) => T
  ) {
  }
  
  get on() {
    return this.events.on;
  }
  
  get trigger() {
    return this.events.trigger;
  }
  
  fetch = async (appFunctions: AppFunctions) => {
    
    let url;
    if (Object.keys(this.apiParams.queryParams).length > 0) {
      url =
        this.apiParams.authParams.url +
        '?' +
        serialize(this.apiParams.queryParams);
    } else {
      url = this.apiParams.authParams.url;
    }
    
    await axios({
      url: url,
      method: 'get',
      timeout: 900000,
      httpsAgent: httpsAgent,
      headers: {
        'Content-Type': `application/x-www-form-urlencoded`,
        Authorization:
          'Basic ' +
          getBasicAuth(
            this.apiParams.authParams.adminName.toLowerCase(),
            this.apiParams.authParams.password
          ),
        Accept: 'application/json;version=2;',
        'aw-tenant-code': this.apiParams.authParams.apikey,
        domain: this.apiParams.authParams.domain,
        adminName: this.apiParams.authParams.adminName.toLowerCase(),
        path: JSON.stringify(this.apiParams.path),
        useCache: await appFunctions.getStateData('stateVal', 'useCache').toString()
      }
    }).then(async (response: AxiosResponse) => {
      
      this.pageSize =
        (await response.data['PageSize']) || (await response.data['pageSize']);
      this.total =
        (await response.data['Total']) || (await response.data['total']);
      this.numberOfPages = Math.ceil(this.total / this.pageSize);
      let currentResponse;
      try {
        currentResponse = await response.data;
        
        
        if (await currentResponse) {
          if (this.apiParams.apiReturnType === 'Array') {
            if (this.apiParams.apiReturnValue !== '') {
              await currentResponse[this.apiParams.apiReturnValue].map(
                async (value: K) => {
                  this.models.push(this.deserialize(value));
                }
              );
            } else {
              await currentResponse.map(async (value: K) => {
                this.models.push(this.deserialize(value));
              });
            }
          } else if (this.apiParams.apiReturnType === 'Object') {
            this.models.push(this.deserialize(await response.data));
          }
        }
      } catch (e) {
        if (e) {
          if (response.data['errorCode']) {
            console.log(`error: ${response.data['errorCode']} message: ${response.data['message']}`);
          } else {
            console.log('error: ', e);
          }
          
        } else {
          appFunctions.setAppState({ errorMessage: 'No Data Received!' });
        }
        
      }
      
      // console.log(`
      // page = ${this.page}
      // pageSize = ${this.pageSize}
      // total = ${this.total}
      // numberOfPages = ${this.numberOfPages}
      //   `);
      
      this.page++;
      // this is to stop the results at whatever page size is
      // this.page = this.numberOfPages;
      if (this.apiParams.queryParams)
        
        if (this.page < this.numberOfPages - 1) {
          for (let i = 0; i < this.numberOfPages - 1; i++) {
            let params;
            if (this.apiParams.queryParams && this.page > 0) {
              params =
                serialize(this.apiParams.queryParams) + '&page=' + String(this.page);
            } else {
              params = 'page=' + String(this.page);
            }
            await axios({
              url: this.apiParams.authParams.url + '?' + params,
              method: 'get',
              timeout: 900000,
              httpsAgent: httpsAgent,
              headers: {
                'Content-Type': `application/x-www-form-urlencoded`,
                Authorization:
                  'Basic ' +
                  getBasicAuth(
                    this.apiParams.authParams.adminName.toLowerCase(),
                    this.apiParams.authParams.password
                  ),
                Accept: 'application/json;version=2;',
                'aw-tenant-code': this.apiParams.authParams.apikey,
                domain: this.apiParams.authParams.domain,
                adminName: this.apiParams.authParams.adminName.toLowerCase(),
                path: JSON.stringify(this.apiParams.path)
              }
            }).then(async (response: AxiosResponse) => {
              // this.page = await response.data['Page'];
              // this.pageSize = await response.data['PageSize'];
              this.total = await response.data['Total'];
              if (await response.data) {
                if (this.apiParams.apiReturnValue !== '') {
                  await response.data[this.apiParams.apiReturnValue].map(
                    async (value: K) => {
                      this.models.push(this.deserialize(value));
                    }
                  );
                } else {
                  await response.data.map(async (value: K) => {
                    this.models.push(this.deserialize(value));
                  });
                }
              }
            });
            this.page++;
          }
        }
    });
    await this.trigger('change');
  };
}
