import * as React from 'react';
import {
  DefaultButton,
  ButtonType, MessageBar, /*, TextField, Text */
  CommandButton
} from '@fluentui/react';

import { ApiParams, AppFunctions, AuthParams, MainPageBodyProps } from '../Components';
import {
  SingleObjectApi, SingleObjectApiProps
} from '../Apis';
import { ApiCollection } from '../Models';

import {
  sortArray,
  buildApiArray, getCurrentTab, loadObjectValFromArray, convertApiDataToSheetAsRange
} from '../Utilities';

import { Stack } from 'office-ui-fabric-react/lib/Stack';
import {
  getDevicesList, getProfileDevicesList,
  getProfilesList,
  updateDeviceProfilesList
} from './CommonFunctions';

export interface ProfilesPageBodyState {
  listStatus?: {};
}

export class ProfilesPageBody extends React.Component<MainPageBodyProps,
  ProfilesPageBodyState> {
  constructor(props) {
    super(props);
    this.state = {
      listStatus: {}
    };
    
    this.handleChangeGroupId = this.handleChangeGroupId.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  handleSubmit(event) {
    event.preventDefault();
  }
  
  handleChangeGroupId(event) {
    this.props.appFunctions.setAppState({ groupid: event.target.value });
  }
  
  getProfile = async (profileId: any, authParams: AuthParams, appFunctions: AppFunctions) => {
    let apiParams: ApiParams = {
      authParams: authParams,
      queryParams: {},
      path: ['v2', 'mdm', 'profiles', profileId],
      apiReturnType: 'Object',
      apiReturnValue: '',
      apiBoostedFields: [
        'General:ProfileUuid',
        'General:ProfileId',
        'General:Name'
      ]
    };
    
    const profile = new ApiCollection(
      apiParams,
      async (json: SingleObjectApiProps) => {
        return await SingleObjectApi.buildObject(json);
      }
    );
    
    await profile.on('change', async () => {
      await convertApiDataToSheetAsRange(
        sortArray(await buildApiArray(await profile.models[0].then((result) => {
          return result['attributes']['data'];
        }), appFunctions.addTimerList), profile.apiParams.apiBoostedFields, true),
        profile.apiParams.apiBoostedFields,
        'Object',
        'Profile_' + profileId,
        '',
        appFunctions.setAppState
      );
    });
    await profile.fetch(appFunctions);
  };
  
  getSelectedProfile = async (authParams: AuthParams, appFunctions) => {
    let profileList;
    if (appFunctions.getStateData('listExists', 'profilesList')) {
      profileList = appFunctions.getListValue('profilesList', 'identifier', appFunctions.getStateData('stateVal', 'adminName'));
    }
    
    if (!profileList) {
      await getProfilesList(true, authParams, appFunctions, this.setLocalState);
    }
    
    await Excel.run(async (context) => {
      let range = await context.workbook.getSelectedRange();
      range.load('address');
      range.load('values');
      return await context.sync().then(async () => {
        range.values.map(async (value) => {
          if (value[0]) {
            await this.getProfile(value[0], authParams, appFunctions);
          }
        });
      });
    }).catch((e) => {
      appFunctions.setAppState({ errorMessage: e.toString() });
    });
  };
  
  
  getCurrentProfileDevices = async (authParams: AuthParams, appFunctions: AppFunctions, setLocalState) => {
    let currentTab = await getCurrentTab();
    let profileUuid = '';
    // appFunctions.deleteValOrList('value', 'currentDeviceProfiles');
    
    if (currentTab[0] === 'Profile') {
      await Excel.run(async (context) => {
        let sheet = context.workbook.worksheets.getActiveWorksheet();
        let range = sheet.getRange('A2');
        range.load('values');
        return await context.sync().then(async () => {
          return range.values.map(async (value) => {
            profileUuid = value[0];
          });
        });
      }).catch((e) => {
        appFunctions.setAppState({ errorMessage: e.toString() });
      });
      
      let apiBoostedFields = ['Id', 'AssignmentType', 'CurrentVersion', 'InstalledProfileVersion', 'Status'];
      let result;
      if (appFunctions.getStateData('listExists', 'profileDevicesList')) {
        result = appFunctions.getListValue('profileDevicesList', 'identifier', profileUuid);
      }
      if (result && result.length === 1) {
        
        
        await convertApiDataToSheetAsRange(
          sortArray(
            await buildApiArray(result[0].list, appFunctions.addTimerList),
            apiBoostedFields, true
          ),
          apiBoostedFields,
          'Array',
          currentTab[3],
          'Devices',
          appFunctions.setAppState
        );
        
      } else {
        
        let profileDevices = await getProfileDevicesList(true, profileUuid, authParams, appFunctions, setLocalState);
        let profileDevicesList = profileDevices[0].list;
        let devices = await getDevicesList(true, authParams, appFunctions, setLocalState);
        let profileList = await getProfilesList(true, authParams, appFunctions, setLocalState);
        
        let blockingSince: number = Date.now();
        let batchSize = 100;
        let modelLength = profileDevicesList.length;
        let allDeviceProfiles;
        for (let h = 0; h < modelLength; h = h + batchSize) {
          
          
          let gets = [];
          
          for (let i = h; i < (modelLength < h + batchSize ? h + (modelLength - h) : h + batchSize); i++) {
            
            gets.push(
              loadObjectValFromArray('Uuid', profileDevicesList[i]['uuid'], devices[0].list).then(async (deviceObject) => {
                if (deviceObject !== 'NotFound') {
                  let tempProfile = await updateDeviceProfilesList(deviceObject['Id']['Value'], authParams, appFunctions, setLocalState);
                  if (allDeviceProfiles) {
                    allDeviceProfiles = [tempProfile, ...allDeviceProfiles];
                  } else {
                    allDeviceProfiles = [tempProfile];
                  }
                  
                }
                
              })
            );
            
          }
          await Promise.all(gets).then(async () => {
            if (await appFunctions.getStateData('stateVal', 'break') === true) {
              h = modelLength;
              await appFunctions.setAppState({ break: false });
            }
            appFunctions.addToAllValues('currentDeviceProfiles', {
              identifier: 'status',
              value: {
                currentPage: h,
                totalPages: modelLength
              }
            });
            setLocalState({ listStatus: { ['currentDeviceProfiles']: appFunctions.getStateData('value', 'currentDeviceProfiles') } });
            
            let now: number = Date.now();
            if (now - blockingSince > 10) {
              await new Promise(resolve => setImmediate(resolve));
              blockingSince = now;
            }
          });
          
        }
        await appFunctions.deleteValOrList('value', 'currentDeviceProfiles');
        setLocalState({ listStatus: { ['currentDeviceProfiles']: {} } });
        let stopFunction = false;
        for (let i = 0; i < profileDevicesList.length; i++) {
          if (await appFunctions.getStateData('stateVal', 'break') === true) {
            stopFunction = true;
            await appFunctions.setAppState({ break: false });
          }
          if (!stopFunction) {
            await loadObjectValFromArray('Uuid', profileDevicesList[i]['uuid'], devices[0].list)
              .then((deviceObject) => {
                if (deviceObject !== 'NotFound') {
                  let deviceProfiles = allDeviceProfiles.filter((obj) => {
                    return obj[0].identifier === deviceObject['Id']['Value'];
                  })[0];
                  // console.log(deviceProfiles);
                  loadObjectValFromArray('ProfileUuid', profileUuid, profileList[0].list)
                    .then(currentProfile => {
                      if (deviceProfiles && deviceProfiles.length > 0) {
                        // console.log(currentProfile);
                        loadObjectValFromArray('Id.Value', currentProfile['ProfileId'], deviceProfiles[0].list)
                          .then((profileData) => {
                            // console.log(profileData);
                            profileDevicesList[i] = {
                              DeviceFriendlyName: deviceObject['DeviceFriendlyName'],
                              UserName: deviceObject['UserName'],
                              Id: deviceObject['Id']['Value'],
                              AssignmentType: profileData['AssignmentType'],
                              CurrentVersion: profileData['CurrentVersion'],
                              InstalledProfileVersion: profileData['InstalledProfileVersion'],
                              Status: profileData['Status'],
                              ...profileDevicesList[i]
                            };
                          });
                      }
                    });
                }
              });
            
            
          }
          if (i % batchSize === 0) {
            appFunctions.addToAllValues('currentDeviceProfiles2', {
              identifier: 'status',
              value: {
                currentPage: i,
                totalPages: modelLength
              }
            });
            setLocalState({ listStatus: { ['currentDeviceProfiles2']: appFunctions.getStateData('value', 'currentDeviceProfiles2') } });
            
          }
          
          let now: number = Date.now();
          if (now - blockingSince > 10) {
            await new Promise(resolve => setImmediate(resolve));
            blockingSince = now;
          }
        }
        // console.log('here');
        await new Promise(resolve => setImmediate(resolve));
        await convertApiDataToSheetAsRange(
          sortArray(
            await buildApiArray(profileDevicesList, appFunctions.addTimerList),
            apiBoostedFields, true
          ),
          apiBoostedFields,
          'Array',
          currentTab[3],
          'Devices',
          appFunctions.setAppState
        );
        // await appFunctions.deleteValOrList('value', 'currentDeviceProfiles');
        // } else {
        //   appFunctions.setAppState({ errorMessage: 'No Data Received' });
        // }
        // });
        //
        // await profileDevices.fetch(appFunctions).then(() => {
        //   setLocalState({ listStatus: { ['currentDeviceProfiles']: appFunctions.getStateData('value', 'currentDeviceProfiles') } });
        // });
        
      }
    }
    
    
  };
  
  //{profileDevicesList: {identifier: "status", value: {currentPage: 1, totalPages: NaN}}}
  setLocalState = async (newStateData: {}) => {
    let start3 = performance.now();
    if (newStateData['listStatus']) {
      // console.log(newStateData);
      let keys = Object.keys(newStateData['listStatus']);
      let newValue = newStateData['listStatus'][keys[0]];
      this.setState({ listStatus: { ...this.state.listStatus, [keys[0]]: newValue } });
    }
    let funcTime = performance.now() - start3;
    await this.props.appFunctions.addTimerList({ functionName: 'setLocalState', totalTime: funcTime });
    // console.log(this.state.listStatus);
  };
  
  render() {
    const { appFunctions } = this.props;
    const authParams = {
      url: appFunctions.getStateData('stateVal', 'url'),
      adminName: appFunctions.getStateData('stateVal', 'adminName'),
      password: appFunctions.getStateData('stateVal', 'password'),
      apikey: appFunctions.getStateData('stateVal', 'apikey'),
      domain: appFunctions.getStateData('stateVal', 'domain')
    };
    // const {url, adminName, password, apikey, domain} = this.state;
    // const textvariants = [
    //   { name: 'tiny' },
    //   { name: 'xSmall' },
    //   { name: 'small' },
    //   { name: 'smallPlus' },
    //   { name: 'medium' },
    //   { name: 'mediumPlus' },
    //   { name: 'large' },
    //   { name: 'xLarge' },
    //   { name: 'xxLarge' },
    //   { name: 'mega' },
    // ];
    let getProfilesListBody, getDevicesListBody, getCurrentProfiledevicesStatusBody,
      getCurrentProfiledevicesStatusBody2;
    if (this.state.listStatus['profilesList'] && JSON.stringify(this.state.listStatus['profilesList']) !== '{}') {
      getProfilesListBody = (
        <MessageBar>
          Getting {this.state.listStatus['profilesList'] && JSON.stringify(this.state.listStatus['profilesList']) !== '{}' ? this.state.listStatus['profilesList']['value']['currentPage'] + ' page of ' : null}
          of {this.state.listStatus['profilesList'] && JSON.stringify(this.state.listStatus['profilesList']) !== '{}' ? this.state.listStatus['profilesList']['value']['totalPages'] + ' pages.' : null}
          <CommandButton style={{ fontSize: 'small' }} iconProps={{ iconName: 'Stop' }}
                         onClick={() => appFunctions.setAppState({ break: true })}>Stop</CommandButton>
        </MessageBar>
      );
    } else {
      getProfilesListBody = null;
    }
    
    if (this.state.listStatus['devicesList'] && JSON.stringify(this.state.listStatus['devicesList']) !== '{}') {
      getDevicesListBody = (
        <MessageBar>
          Updating Devices List... <br/>
          Getting {this.state.listStatus['devicesList'] && JSON.stringify(this.state.listStatus['devicesList']) !== '{}' ? this.state.listStatus['devicesList']['value']['currentPage'] + ' page of ' : null}
          of {this.state.listStatus['devicesList'] && JSON.stringify(this.state.listStatus['devicesList']) !== '{}' ? this.state.listStatus['devicesList']['value']['totalPages'] + ' pages.' : null}
          <CommandButton style={{ fontSize: 'small' }} iconProps={{ iconName: 'Stop' }}
                         onClick={() => appFunctions.setAppState({ break: true })}>Stop</CommandButton>
        </MessageBar>
      );
    } else {
      getDevicesListBody = null;
    }
    
    if (this.state.listStatus['currentDeviceProfiles'] && JSON.stringify(this.state.listStatus['currentDeviceProfiles']) !== '{}') {
      getCurrentProfiledevicesStatusBody = (
        <MessageBar>
          Getting {this.state.listStatus['currentDeviceProfiles'] && JSON.stringify(this.state.listStatus['currentDeviceProfiles']) !== '{}' ? this.state.listStatus['currentDeviceProfiles']['value']['currentPage'] + ' device of ' : null}
          of {this.state.listStatus['currentDeviceProfiles'] && JSON.stringify(this.state.listStatus['currentDeviceProfiles']) !== '{}' ? this.state.listStatus['currentDeviceProfiles']['value']['totalPages'] + ' devices.' : null}
          <CommandButton style={{ fontSize: 'small' }} iconProps={{ iconName: 'Stop' }}
                         onClick={() => appFunctions.setAppState({ break: true })}>Stop</CommandButton>
        </MessageBar>
      );
    } else {
      getCurrentProfiledevicesStatusBody = null;
    }
    
    if (this.state.listStatus['currentDeviceProfiles2'] && JSON.stringify(this.state.listStatus['currentDeviceProfiles2']) !== '{}') {
      getCurrentProfiledevicesStatusBody2 = (
        <MessageBar>
          Stage2 -
          Processing {this.state.listStatus['currentDeviceProfiles2'] && JSON.stringify(this.state.listStatus['currentDeviceProfiles2']) !== '{}' ? this.state.listStatus['currentDeviceProfiles2']['value']['currentPage'] + ' device of ' : null}
          of {this.state.listStatus['currentDeviceProfiles2'] && JSON.stringify(this.state.listStatus['currentDeviceProfiles2']) !== '{}' ? this.state.listStatus['currentDeviceProfiles2']['value']['totalPages'] + ' devices.' : null}
          <CommandButton style={{ fontSize: 'small' }} iconProps={{ iconName: 'Stop' }}
                         onClick={() => appFunctions.setAppState({ break: true })}>Stop</CommandButton>
        </MessageBar>
      );
    } else {
      getCurrentProfiledevicesStatusBody2 = null;
    }
    
    const stackTokens = { childrenGap: 5, maxWidth: 300 };
    return (
      <Stack tokens={stackTokens}>
        <br/>
        <Stack.Item align='center'>
          <h3>Profiles Page</h3>
        </Stack.Item>
        <p>
          Choose to get a list of your Profiles or select a Profile Id on your
          list and get various details about your Profile
        </p>
        <Stack.Item align='center'>
          <DefaultButton
            type='submit'
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={() => {
              setTimeout(() => {
                this.setState({ listStatus: { ['profilesList']: this.props.appFunctions.getStateData('value', 'profilesList') } });
              }, 100);
              getProfilesList(false, authParams, appFunctions, this.setLocalState).then(() => {
                  setTimeout(() => {
                    this.setState({ listStatus: { ['profilesList']: {} } });
                  }, 250);
                }
              );
              
            }
              // this.getProfilesList(false, authParams, appFunctions.setAppState, appFunctions.addToAllLists, appFunctions.getStateData, appFunctions.getListValue, appFunctions.addTimerList)
            }
          >
            Get Profiles List
          </DefaultButton>
          {getProfilesListBody}
        </Stack.Item>
        <Stack.Item align='center'>
          <DefaultButton
            type='submit'
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={() => this.getSelectedProfile(authParams, appFunctions)}
          >
            Get Selected Profile(s) Data
          </DefaultButton>
        </Stack.Item>
        <Stack.Item align='center'>
          <DefaultButton
            type='submit'
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={() => {
              appFunctions.addToAllValues('devicesList', {
                identifier: 'status',
                value: {
                  currentPage: 1,
                  totalPages: 0
                }
              });
              setTimeout(() => {
                this.setState({ listStatus: { ['devicesList']: this.props.appFunctions.getStateData('value', 'devicesList') } });
              }, 1000);
              setTimeout(() => {
                this.setState({ listStatus: { ['currentDeviceProfiles']: this.props.appFunctions.getStateData('value', 'currentDeviceProfiles') } });
              }, 2000);
              this.getCurrentProfileDevices(authParams, appFunctions, this.setLocalState).then(() => {
                  setTimeout(() => {
                    this.setState({ listStatus: { ['currentDeviceProfiles']: this.props.appFunctions.getStateData('value', 'currentDeviceProfiles') } });
                  }, 2000);
                }
              );
              
            }}
          >
            Get Current Profile Devices
          </DefaultButton>
          {getDevicesListBody}
          {getCurrentProfiledevicesStatusBody}
          {getCurrentProfiledevicesStatusBody2}
        </Stack.Item>
      </Stack>
    );
  }
}
