import * as React from 'react';
import { DefaultButton, ButtonType, TextField } from '@fluentui/react';

export interface SuccessPageBodyProps {
  logout: () => {};
}


export interface SuccessPageBodyState {
  /* The field values */
  value: string;
}

export class SuccessPageBody extends React.Component<SuccessPageBodyProps, SuccessPageBodyState> {
  constructor(props) {
    super(props);
    this.state = { value: '' };
    
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  handleChange(event) {
    this.setState({ value: event.target.value });
  }
  
  handleSubmit(event) {
    event.preventDefault();
  }
  
  render() {
    
    const { logout } = this.props;
    
    return (
      
      <div className='ms-welcome__main'>
        <form onSubmit={this.handleSubmit}>
          <TextField type="text" value={this.state.value} onChange={this.handleChange} label="Chart Number"
                     placeholder="Enter Chart Number"/>
          <DefaultButton type="submit"
                         className='ms-welcome__action'
                         buttonType={ButtonType.hero}
                         iconProps={{ iconName: 'ChevronRight' }}
          >Get Chart Number</DefaultButton>
        </form>
        <DefaultButton className='ms-welcome__action' buttonType={ButtonType.hero}
                       iconProps={{ iconName: 'ChevronRight' }}
                       onClick={logout}>Log Out</DefaultButton>
      </div>
    );
  }
}
