import * as React from 'react';
import { DefaultButton, ButtonType, MessageBar, CommandButton } from '@fluentui/react';
import { genericObjectFunc } from '../Apis/genericObjectFunction';

import { ApiReturnType, AppFunctions, AuthParams, MainPageBodyProps } from '../Components';
import {
  genericListFunc
} from '../Apis';
import {
  getCurrentTab
} from '../Utilities';
import { Stack } from 'office-ui-fabric-react/lib/Stack';
import { getDevicesList } from './CommonFunctions';
import { ProgressBar } from './ProgressBar';

export interface DevicesPageBodyState {
  listStatus?: {};
}

export class DevicesPageBody extends React.Component<MainPageBodyProps, DevicesPageBodyState> {
  constructor(props) {
    super(props);
    this.state = {
      listStatus: {}
      
    };
    
    this.handleChangeGroupId = this.handleChangeGroupId.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  value = '';
  
  handleSubmit(event) {
    event.preventDefault();
  }
  
  handleChangeGroupId(event) {
    this.props.appFunctions.setAppState({ groupid: event.target.value });
  }
  
  getDevice = async (deviceId: string, authParams, appFunctions) => {
    let path = ['mdm', 'devices', deviceId];
    let queryParams = {};
    let apiReturnType: ApiReturnType = 'Object';
    let apiReturnValue = 'Device';
    let apiBoostedFields = [
      'Id:Value',
      'Udid',
      'SerialNumber',
      'DeviceFriendlyName',
      'EnrollmentStatus',
      'ComplianceStatus',
      'CompromisedStatus',
      'LastSeen',
      'Platform',
      'UserName',
      'Model',
      'OperatingSystem'
    ];
    let localOnly = false;
    let sheetTitle = 'Device_' + deviceId;
    let addOnDataType = '';
    let valueName = 'myObject';
    
    
    await genericObjectFunc(
      authParams,
      path,
      queryParams,
      apiReturnType,
      apiReturnValue,
      apiBoostedFields,
      localOnly,
      sheetTitle,
      addOnDataType,
      valueName,
      appFunctions
    );
    
    
  };
  
  
  getSelectedDevice = async (authParams, appFunctions: AppFunctions) => {
    Excel.run((context) => {
      let range = context.workbook.getSelectedRange();
      range.load('address');
      range.load('values');
      
      return context.sync().then(() => {
        range.values.map((value) => {
          if (value[0]) {
            this.getDevice(value[0], authParams, appFunctions);
          }
          
        });
      });
    }).catch((e) => {
      appFunctions.setAppState({ errorMessage: e.toString() });
    });
  };
  
  getCurrentDeviceApps = async (authParams: AuthParams,
                                appFunctions, setLocalState) => {
    let currentTab = await getCurrentTab();
    // console.log(currentTab);
    let deviceId = await currentTab[1];
    
    if (currentTab[0] === 'Device') {
      let path = ['mdm', 'devices', deviceId, 'apps'];
      let queryParams = { pagesize: 5000 };
      let apiReturnType: ApiReturnType = 'Array';
      let apiReturnValue = 'DeviceApps';
      let apiBoostedFields = ['Id:Value', 'ApplicationName', 'Type'];
      let localOnly = false;
      let sheetTitle = 'Device_' + deviceId;
      let addOnDataType = 'Apps';
      let listName = 'deviceApps';
      let identifier = deviceId;
      let updateLocalState = true;
      
      
      await genericListFunc(
        authParams,
        path,
        queryParams,
        apiReturnType,
        apiReturnValue,
        apiBoostedFields,
        localOnly,
        sheetTitle,
        addOnDataType,
        listName,
        identifier,
        updateLocalState,
        appFunctions,
        setLocalState
      );
      
      
    } else {
      return null;
    }
  };
  // mounted = false;
  
  
  setLocalState = async (newStateData: {}) => {
    this.setState(newStateData);
  };
  
  //sdfdsf
  // componentDidMount() {
  //   this.mounted = true;
  // }
  //
  // componentWillUnmount() {
  //   this.mounted = false;
  // }
  
  render() {
    const { appFunctions } = this.props;
    const authParams = {
      url: appFunctions.getStateData('stateVal', 'url'),
      adminName: appFunctions.getStateData('stateVal', 'adminName'),
      password: appFunctions.getStateData('stateVal', 'password'),
      apikey: appFunctions.getStateData('stateVal', 'apikey'),
      domain: appFunctions.getStateData('stateVal', 'domain')
    };
    // const textvariants = [
    //   { name: 'tiny' },
    //   { name: 'xSmall' },
    //   { name: 'small' },
    //   { name: 'smallPlus' },
    //   { name: 'medium' },
    //   { name: 'mediumPlus' },
    //   { name: 'large' },
    //   { name: 'xLarge' },
    //   { name: 'xxLarge' },
    //   { name: 'mega' },
    // ];
    let getDevicesListBody, getCurrentDeviceAppsStatusBody;
    if (this.state.listStatus['devicesList'] && JSON.stringify(this.state.listStatus['devicesList']) !== '{}') {
      getDevicesListBody = (
        <MessageBar>
          Getting {this.state.listStatus['devicesList'] && JSON.stringify(this.state.listStatus['devicesList']) !== '{}' ? this.state.listStatus['devicesList']['value']['currentPage'] + ' page of ' : null}
          of {this.state.listStatus['devicesList'] && JSON.stringify(this.state.listStatus['devicesList']) !== '{}' ? this.state.listStatus['devicesList']['value']['totalPages'] + ' pages.' : null}
          <CommandButton style={{ fontSize: 'small' }} iconProps={{ iconName: 'Stop' }}
                         onClick={() => appFunctions.setAppState({ break: true })}>Stop</CommandButton>
        </MessageBar>
      );
    } else {
      getDevicesListBody = null;
    }
    
    if (this.state.listStatus['deviceApps'] && JSON.stringify(this.state.listStatus['deviceApps']) !== '{}') {
      getCurrentDeviceAppsStatusBody = (
        <MessageBar>
          Getting {this.state.listStatus['deviceApps'] && JSON.stringify(this.state.listStatus['deviceApps']) !== '{}' ? this.state.listStatus['deviceApps']['value']['currentPage'] + ' page of ' : null}
          of {this.state.listStatus['deviceApps'] && JSON.stringify(this.state.listStatus['deviceApps']) !== '{}' ? this.state.listStatus['deviceApps']['value']['totalPages'] + ' pages.' : null}
          <CommandButton style={{ fontSize: 'small' }} iconProps={{ iconName: 'Stop' }}
                         onClick={() => appFunctions.setAppState({ break: true })}>Stop</CommandButton>
        </MessageBar>
      );
    } else {
      getCurrentDeviceAppsStatusBody = null;
    }
    
    
    const stackTokens = { childrenGap: 0, maxWidth: 300 };
    
    return (
      <Stack tokens={stackTokens}>
        <Stack.Item align='center'>
          <h3>Devices Page</h3>
        </Stack.Item>
        <p>
          Choose to get a list of your devices or select a device Id on your
          list and get various details about your device
        </p>
        
        <Stack.Item align='center'>
          <DefaultButton
            type='submit'
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={() => {
              setTimeout(() => {
                this.setState({ listStatus: { ['devicesList']: this.props.appFunctions.getStateData('value', 'devicesList') } });
              }, 100);
              getDevicesList(false, authParams, appFunctions, this.setLocalState).then(() => {
              
              });
            }}
          >
            Get Device List
          </DefaultButton>
          {getDevicesListBody}
        </Stack.Item>
        <Stack.Item>
          {appFunctions.getStateData('stateVal', 'value') === '1' ? (
            <ProgressBar
              itemType='Device'
            />
          ) : null}
        </Stack.Item>
        <Stack.Item align='center'>
          <DefaultButton
            type='submit'
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={() => this.getSelectedDevice(authParams, appFunctions)}
          >
            Get Selected Device(s) Data
          </DefaultButton>
        </Stack.Item>
        <Stack.Item align='center'>
          <DefaultButton
            type='submit'
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={() => {
              setTimeout(() => {
                this.setState({ listStatus: { ['deviceApps']: this.props.appFunctions.getStateData('value', 'deviceApps') } });
              }, 100);
              this.getCurrentDeviceApps(authParams, appFunctions, this.setLocalState).then(() => {
                  // if (this.mounted) {
                  //   this.setState({ listStatus: { ['deviceApps']: this.props.appFunctions.getStateData('value', 'deviceApps') } });
                  // }
                  
                }
              );
              
            }}
          >
            Get Current Device Apps
          </DefaultButton>
          {getCurrentDeviceAppsStatusBody}
        </Stack.Item>
        
        {/*<br/>*/}
        {/*<form onSubmit={this.handleSubmit} className='ms-welcome__form'>*/}
        {/*  <Text block variant={'medium'}>*/}
        {/*    Enter a device ID below to get a specific one*/}
        {/*  </Text>*/}
        {/*  <Stack.Item align='center'>*/}
        {/*    <TextField*/}
        {/*      type='text'*/}
        {/*      value={this.value}*/}
        {/*      onChange={(event) => {*/}
        {/*        appFunctions.setAppState({ value: event.currentTarget.value });*/}
        {/*      }}*/}
        {/*      label='Device Id'*/}
        {/*      placeholder='Enter Device Id'*/}
        {/*    />*/}
        {/*  </Stack.Item>*/}
        {/*  <Stack.Item align='center'>*/}
        {/*    <DefaultButton*/}
        {/*      type='submit'*/}
        {/*      className='ms-welcome__action'*/}
        {/*      buttonType={ButtonType.hero}*/}
        {/*      iconProps={{ iconName: 'ChevronRight' }}*/}
        {/*      onClick={() => this.getDevice(this.value, authParams, appFunctions)}*/}
        {/*    >*/}
        {/*      Get Device Data*/}
        {/*    </DefaultButton>*/}
        {/*  </Stack.Item>*/}
        {/*</form>*/}
      </Stack>
    );
  }
}
