import * as React from 'react';
import { Pivot, PivotItem } from 'office-ui-fabric-react/lib/Pivot';
import { DevicesPageBody, LoggedInPageBody } from './index';
import {
  MainPageBodyProps, MainPageBodyState
} from '../Components';
import { ButtonType, DefaultButton, IComboBox, IComboBoxOption, Toggle, Text, ComboBox } from '@fluentui/react';
import { AppsPageBody } from './AppsPageBody';
import { Stack } from 'office-ui-fabric-react/lib/Stack';
import { cleanupSheets } from '../Utilities';
import { ProfilesPageBody } from './ProfilesPageBody';
import { BaselinesPageBody } from './BaselinesPageBody';


export class MainPageBody extends React.Component<MainPageBodyProps,
  MainPageBodyState> {
  constructor(props) {
    super(props);
    this.state = {
      value: ''
    };
    
    this.handleChangeGroupId = this.handleChangeGroupId.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  handleSubmit(event) {
    event.preventDefault();
  }
  
  handleChangeGroupId(event) {
    this.props.appFunctions.setAppState({ groupid: event.target.value });
  }
  
  onChange(_ev: React.FormEvent<IComboBox>, option?: IComboBoxOption) {
    // console.log(option.key);
    this.props.appFunctions.setAppState({ groupid: option.key });
  }
  
  render() {
    const {
      appFunctions
    } = this.props;
    
    // const labelStyles: Partial<IStyleSet<ILabelStyles>> = {
    //   root: { marginTop: 10, marginLeft: 5 }
    // };
    
    // let myoptions = appFunctions.getStateData('stateVal', 'roles').map((data) => (
    //   <option key={data.LocationGroupId} value={data.LocationGroupId}>
    //     {data.LocationGroup}
    //   </option>
    // ));
    let items: IComboBoxOption[];
    if (appFunctions.getStateData('listExists', 'groupList')) {
      items = appFunctions.getStateData('list', 'groupList')[0].list.map((data) => (
        {
          key: data['LocationGroupId'],
          text: data['LocationGroupName'] + ' Count:' + data['TotalDevices'],
          count: data['TotalDevices']
        }
      )).sort(function(a, b) {
        return b.count - a.count;
      });
    }
    
    async function _onChangeUseCache(
      _ev: React.FormEvent<HTMLElement>,
      isChecked: boolean
    ) {
      // console.log(`The option has been changed to ${isChecked}.`);
      await appFunctions.setAppState({ useCache: !!isChecked });
    }
    
    async function _onChangeFilterOnGroupId(
      _ev: React.FormEvent<HTMLElement>,
      isChecked: boolean
    ) {
      // console.log(`The option has been changed to ${isChecked}.`);
      await appFunctions.setAppState({ filterOnGroupId: !!isChecked });
    }
    
    
    const stackTokens = { childrenGap: 0, padding: 5, maxWidth: 350 };
    // const pivotTokens: IPivotProps = {
    //   linkSize: 0,
    // };
    return (
      <Stack tokens={stackTokens}>
        <Pivot
          linkSize={0}
          aria-label='Main Menu' /*style={{ width: 300 }}*/
        
        >
          <PivotItem
            headerText='Main'
            headerButtonProps={{
              'data-order': 1,
              'data-title': 'Main Page'
            }}
            alwaysRender={true}
          >
            <LoggedInPageBody
              appFunctions={appFunctions}
            />
          </PivotItem>
          <PivotItem
            headerText='UEM'
            headerButtonProps={{
              'data-order': 1,
              'data-title': 'UEM Pages'
            }}
            alwaysRender={true}
          >
            <Pivot
              linkSize={0}
              aria-label='Main Menu' /*style={{ width: 300 }}*/
            >
              <PivotItem
                headerText='Devices'
                headerButtonProps={{
                  'data-order': 2,
                  'data-title': 'Devices Page'
                }}
                alwaysRender={true}
              >
                <DevicesPageBody
                  appFunctions={appFunctions}
                />
              </PivotItem>
              <PivotItem
                headerText='Apps'
                headerButtonProps={{
                  'data-order': 3,
                  'data-title': 'Apps Page'
                }}
                alwaysRender={true}
              >
                <AppsPageBody
                  appFunctions={appFunctions}
                />
              </PivotItem>
              <PivotItem
                headerText='Profiles'
                headerButtonProps={{
                  'data-order': 4,
                  'data-title': 'Profiles Page'
                }}
                alwaysRender={true}
              >
                <ProfilesPageBody
                  appFunctions={appFunctions}
                />
              </PivotItem>
              <PivotItem
                headerText='Baselines'
                headerButtonProps={{
                  'data-order': 4,
                  'data-title': 'Profiles Page'
                }}
                alwaysRender={true}
              >
                <BaselinesPageBody
                  appFunctions={appFunctions}
                />
              </PivotItem>
              <PivotItem headerText='Options' alwaysRender={true}>
                <Stack tokens={{ childrenGap: 5 }}>
                  <Stack.Item align='center'>
                    <h3>Options Page</h3>
                  </Stack.Item>
                  <Stack.Item align='start'>
                    <Text block variant={'medium'}>
                      Your GroupId is shown below. If you have more than one role,
                      you can choose a different GroupId to be the base for your
                      queries.
                    </Text>
                  </Stack.Item>
                  <Stack.Item align='start'>
                    <Text>Pick your GroupId</Text>
                  </Stack.Item>
                  <Stack.Item>
                    <ComboBox
                      selectedKey={parseInt(appFunctions.getStateData('stateVal', 'groupid'))}
                      label="Controlled single-select ComboBox (allowFreeform: T)"
                      allowFreeform
                      autoComplete="on"
                      options={items}
                      onChange={(_ev: React.FormEvent<IComboBox>, option?: IComboBoxOption) => {
                        appFunctions.setAppState({ groupid: option.key });
                      }}
                    />
                  </Stack.Item>
                  <Stack.Item align='start'>
                    <Stack.Item align='center'>
                      <Toggle
                        label="Filter on GroupId"
                        defaultChecked={false}
                        onText="On"
                        offText="Off"
                        onChange={_onChangeFilterOnGroupId}
                        role="checkbox"
                      />
                      <Text block variant={'small'}>Filter Results on selected GroupId</Text>
                      <Text block variant={'small'}>(Normally Off to see all results)</Text>
                    </Stack.Item>
                  </Stack.Item>
                </Stack>
              </PivotItem>
            </Pivot>
          </PivotItem>
        </Pivot>
        <Stack.Item align='center'>
          <DefaultButton
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={appFunctions.logout}
          >
            Log Out
          </DefaultButton>
        </Stack.Item>
        <Stack.Item align='center'>
          <DefaultButton
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={cleanupSheets}
          >
            Cleanup Sheets
          </DefaultButton>
        </Stack.Item>
        <Stack.Item align='center'>
          <Toggle
            label="Use Cached Data"
            defaultChecked
            onText="On"
            offText="Off"
            onChange={_onChangeUseCache}
            role="checkbox"
          />
          <span>Turn off to get fresh data</span>
        </Stack.Item>
      </Stack>
    );
  }
}
