export const buildApiArray = async function(obj: object, addTimerList): Promise<string[][]> {
  let output: object = {};
  let maxLength: number = 0;
  let blockingSince: number = Date.now();
  let start = performance.now();


  async function flatten(
    obj: object,
    arrayLevel: number,  // array nesting level
    index: number,       // column index in the output
    header: string): Promise<void> {
    // console.log(`arrayLevel: ${arrayLevel}, index: ${index}`);


    if (Array.isArray(obj)) {
      arrayLevel++;
    }
    for (let key in obj) {
      let value: any = obj[key];
      let newHeader: string;
      if (Array.isArray(obj) && arrayLevel == 1) {
        newHeader = header;
        index = key as unknown as number;
      } else {
        newHeader = header == '' ? key : header + ':' + key;
      }
      if (['string', 'boolean', 'number'].includes(typeof value)) {
        if (newHeader in output) {
          output[newHeader][index] = value;
        } else {
          output[newHeader] = [value];
        }
        maxLength = Math.max(maxLength, output[newHeader].length);
      } else {
        await flatten(value, arrayLevel, index, newHeader);
      }
    }

    // https://snyk.io/blog/nodejs-how-even-quick-async-functions-can-block-the-event-loop-starve-io/
    let now: number = Date.now();
    if (now - blockingSince > 10) {
      await new Promise(resolve => setImmediate(resolve));
      blockingSince = now;
    }
  }

  await flatten(obj, 0, 0, '');

  // convert to array, replacing null values with empty string
  let range: number[] = [];
  for (let i = 0; i < maxLength; i++) {
    range.push(i);
  }
  let funcTime = performance.now() - start;
  addTimerList({ functionName: 'BuildApiArray', totalTime: funcTime });
  return Object.entries(output).map(entry =>
    [entry[0]].concat(range.map(i => typeof entry[1][i] === 'undefined' ? '' : entry[1][i]))
  );
};

