const refreshSheetAsRange = async (
  context: Excel.RequestContext,
  boostedFields: string[],
  dataType: string,
  columnLetters: string[],
  data: string[][],
  sheetTitle: string,
  addOnDataType: string,
  startingRow: number,
  setAppState: ({}) => void
) => {
  OfficeExtension.config.extendedErrorLogging = true;
  
  const sheet = context.workbook.worksheets.getItem(sheetTitle);
  sheet.load().activate();
  let table: Excel.Table;
  let tableName;
  let tableExists = false;
  let tableList = sheet.tables.load();
  await context.sync();
  
  
  if (addOnDataType.length > 0) {
    
    tableName = `${sheetTitle}Table${addOnDataType}`;
    table = sheet.tables.getItemOrNullObject(
      tableName
    );
    await context.sync();
    if (table.isNullObject) {
      
      let dataWidth = data[0].length;
      let dataLength = data.length;
      for (let i = 0; i < dataLength; i + 1000 > dataLength ? i = dataLength : i = i + 1000) {
        // console.log('A' + (i + startingRow) + ':' + columnLetters[dataWidth - 1] + (i + 1000 > dataLength ? dataLength - 1 + startingRow : i + 1000 + startingRow));
        let range = sheet.getRange('A' + (i + startingRow) + ':' + columnLetters[dataWidth - 1] + (i + 1000 > dataLength ? dataLength - 1 + startingRow : i + 1000 - 1 + startingRow));
        range.values = data.slice(i, (i + 1000 > dataLength ? dataLength : i + 1000));
        sheet.activate();
        await context.sync();
      }
      
      // Convert the range to a table
      let table = sheet.tables.add('A' + startingRow + ':' + columnLetters[dataWidth - 1] + (dataLength - 1 + startingRow), true);
      table.name = tableName;
    } else {
      table = sheet.tables.getItem(tableName);
      await context.sync();
      tableExists = true;
    }
    
  } else {
    if (tableList.count > 1) {
      setAppState({ errorMessage: 'Excel does not allow manipulation of higher row tables ( eg. The top table) when there are more than one tables on a sheet.  You can refresh data on the lowest table only.' });
      return;
    }
    tableName = sheetTitle + 'Table';
    
    table = sheet.tables.getItemOrNullObject(tableName);
    
    await context.sync();
    // .catch(e => {
    //   if (e.toString().substr(0, 12) === 'ItemNotFound') {
    //     tableExists = false;
    //   }
    // });
    tableExists = table !== null;
  }
  
  if (tableExists) {
    table.load();
    table.delete();
    // table.convertToRange();
    
    await context.sync();
    let dataWidth = data[0].length;
    let dataLength = data.length;
    for (let i = 0; i < dataLength; i + 1000 > dataLength ? i = dataLength : i = i + 1000) {
      let range: Excel.Range;
      if (addOnDataType.length > 1) {
        range = sheet.getRange('A' + (i + startingRow) + ':' + columnLetters[dataWidth - 1] + (i + 1000 > dataLength ? dataLength - 1 + startingRow : i + 1000 + startingRow - 1));
      } else {
        range = sheet.getRange('A' + (i + 1 + startingRow) + ':' + columnLetters[dataWidth - 1] + (i + 1000 > dataLength ? dataLength + startingRow : i + 1000 + startingRow));
      }
      
      range.values = data.slice(i, (i + 1000 > dataLength ? dataLength : i + 1000));
      sheet.activate();
      await context.sync();
    }
    
    sheet.getUsedRange().getEntireColumn().format.autofitColumns();
    sheet.getUsedRange().getEntireRow().format.autofitRows();
    sheet.activate();
    
    
    // Convert the range to a table
    if (addOnDataType.length > 0) {
      table = sheet.tables.add('A' + (startingRow) + ':' + columnLetters[dataWidth - 1] + (dataLength - 1 + startingRow), true);
    } else {
      table = sheet.tables.add('A' + (startingRow + 1) + ':' + columnLetters[dataWidth - 1] + (dataLength + startingRow), true);
    }
    
    table.name = tableName;
    
    
    await context.sync();
  }
  
  sheet.getUsedRange().getEntireColumn().format.autofitColumns();
  sheet.getUsedRange().getEntireRow().format.autofitRows();
  sheet.activate();
  
  if (dataType === 'Object') {
    let tempTitleArray = [];
    let tempValueArray = [];
    for (let i = 0; i < boostedFields.length; i++) {
      let sourceTitleCell = sheet.getCell(0, i);
      sourceTitleCell.load('values');
      tempTitleArray.push(sourceTitleCell);
      let sourceValueCell = sheet.getCell(1, i);
      sourceValueCell.load('values');
      tempValueArray.push(sourceValueCell);
    }
    
    await context.sync();
    for (let i = 5; i < boostedFields.length + 5; i++) {
      let cell = sheet.getRange('A' + i);
      cell.values = tempTitleArray[i - 5].values;
      
      let value = sheet.getRange('B' + i);
      value.values = tempValueArray[i - 5].values;
    }
    let type = sheetTitle.split('_')[0];
    let cell = sheet.getRange('A4');
    cell.values = [[type + ' Details']];
    cell.format.autofitColumns();
  }
  
  await context.sync();
};
export default refreshSheetAsRange;
