import initializeNewSheetAsRange from './initializeNewSheetAsRange';
import refreshSheetAsRange from './refreshSheetAsRange';


function fixName(longName: string): string {
  return longName.replace(/[ $%#^@&*()]/g, '_').replace(/^(.{27}).{2,}/, '$1…');
}

export async function convertApiDataToSheetAsRange(
  apiData: string[][],
  boostedFields: string[],
  dataType: string,
  sheetTitle: string,
  addOnDataType: string,
  setAppState: ({}) => void
) {
  
  sheetTitle = fixName(sheetTitle);
  await Excel.run(async (context: Excel.RequestContext) => {
    let worksheet = context.workbook.worksheets.getItemOrNullObject(sheetTitle);
    await context.sync();
    let startingRow = 0;
    
    switch (addOnDataType) {
      case 'Apps': {
        startingRow = 18;
        break;
      }
      case 'Devices': {
        startingRow = 19;
        break;
      }
      case 'Profiles': {
        startingRow = 20;
        break;
      }
      default: {
        startingRow = 0;
        break;
      }
    }
    
    
    const columnLetters: string[] = [];
    const alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
    for (let x = 0; x < alphabet.length; x++) {
      for (let y = 0; y < alphabet.length; y++) {
        if (x === 0) {
          columnLetters.push(alphabet[y]);
        } else {
          columnLetters.push(alphabet[x - 1] + alphabet[y]);
        }
      }
    }
    
    if (apiData && apiData.length > 0 && apiData[0]) {
      if (JSON.stringify(worksheet) === '{}') {
        await initializeNewSheetAsRange(
          context,
          boostedFields,
          dataType,
          columnLetters,
          apiData,
          sheetTitle
        );
      } else {
        await refreshSheetAsRange(
          context,
          boostedFields,
          dataType,
          columnLetters,
          apiData,
          sheetTitle,
          addOnDataType,
          startingRow,
          setAppState
        );
      }
    } else {
      setAppState({ errorMessage: 'No Data Received' });
    }
  });
}
