import * as React from 'react';
import {
  DefaultButton,
  ButtonType, /*, TextField, Text */
  MessageBar, CommandButton
  
} from '@fluentui/react';

import {
  ApiParams, ApiReturnType, AppFunctions,
  AuthParams,
  MainPageBodyProps
} from '../Components';
import {
  genericListFunc, SingleObjectApi, SingleObjectApiProps
} from '../Apis';
import { ApiCollection } from '../Models';
import { getAppsList, getDeviceApps } from './CommonFunctions';

import {
  buildApiArray, convertApiDataToSheetAsRange, getColumnLetters,
  getCurrentTab
} from '../Utilities';
import { sortArray } from '../Utilities';
import { Stack } from 'office-ui-fabric-react/lib/Stack';

// import { StartPageBody } from './StartPageBody';

export interface AppsPageBodyState {
  listStatus?: {};
}


export class AppsPageBody extends React.Component<MainPageBodyProps, AppsPageBodyState> {
  constructor(props) {
    super(props);
    this.state = {
      listStatus: {}
      
    };
    
    this.handleChangeGroupId = this.handleChangeGroupId.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  handleSubmit(event) {
    event.preventDefault();
  }
  
  handleChangeGroupId(event) {
    this.props.appFunctions.setAppState({ groupid: event.target.value });
  }
  
  showAppListProgress = false;
  appListCurrentItem: number = null;
  appListTotalItems: number = null;
  
  
  getPublicApp = async (appId: any, authParams: AuthParams, appFunctions, setLocalState) => {
    let appsList = await getAppsList(true, authParams, appFunctions, setLocalState);
    
    async function loadPublicApp(nameKey, myArray) {
      for (let i = 0; i < (await myArray.length); i++) {
        if ((await myArray[i].Id.Value) === nameKey && await myArray[i].AppType === 'Public') {
          return await myArray[i];
        }
      }
    }
    
    const publicApp = await loadPublicApp(appId, appsList[0].list);
    await convertApiDataToSheetAsRange(
      sortArray(await buildApiArray(publicApp, appFunctions.addTimerList), [
        'Id:Value',
        'ApplicationName',
        'AppType',
        'Platform',
        'BundleId',
        'ApplicationSize',
        'ApplicationUrl'
      ], true),
      [
        'Id:Value',
        'ApplicationName',
        'AppType',
        'Platform',
        'BundleId',
        'ApplicationSize',
        'ApplicationUrl'
      ],
      'Object',
      'App_' + appId + '_p',
      '',
      appFunctions.setAppState
    );
  };
  
  getAppVals = (
    range: string,
    columnToTheRight: number
  ): string[] => {
    const columnLetters = getColumnLetters();
    if (range.indexOf('!') && range.indexOf('!') > 0) {
      range = range.split('!')[1];
    }
    let start = range;
    let end = range;
    if (range.indexOf(':') && range.indexOf(':') > 0) {
      start = range.split(':')[0];
      end = range.split(':')[1];
    }
    
    let rowStartArr = start.match(/([0-9]){1,3}/g);
    let rowStart = 0;
    if (rowStartArr && rowStartArr.length > 0) {
      rowStart = parseInt(rowStartArr[0]);
    }
    let rowEndArr = end.match(/([0-9]){1,3}/g);
    let rowEnd = 0;
    if (rowEndArr && rowEndArr.length > 0) {
      rowEnd = parseInt(rowEndArr[0]);
    }
    let columnStartArr = start.match(/([A-Z]){1,3}/g);
    let columnStart = '';
    if (columnStartArr && columnStartArr.length > 0) {
      columnStart = columnStartArr[0];
    }
    let columnWithValue = columnLetters.indexOf(columnStart) + columnToTheRight;
    // let columnEnd = end.match(/([A-Z]){1,3}/g);
    let addressArray = [];
    let numRows: number;
    numRows = rowEnd - rowStart + 1;
    for (let i = 0; i < numRows; i++) {
      addressArray.push(`${columnLetters[columnWithValue]}${rowStart + i}`);
    }
    // console.log(`
    //   rowStart: ${rowStart}
    //   rowEnd: ${rowEnd}
    //   columnStart: ${columnStart}
    //   addressArray: ${addressArray}
    //   `);
    
    return addressArray;
  };
  
  getInternalApp = async (appId: any, authParams: AuthParams, appFunctions) => {
    let apiParams: ApiParams = {
      authParams: authParams,
      queryParams: {},
      path: ['mam', 'apps', 'internal', appId],
      apiReturnType: 'Object',
      apiReturnValue: 'InternalApp',
      apiBoostedFields: [
        'id',
        'ApplicationName',
        'AppType',
        'Platform',
        'uuid',
        'AppSizeInKB'
      ]
    };
    
    const app = new ApiCollection(
      apiParams,
      async (json: SingleObjectApiProps) => {
        return await SingleObjectApi.buildObject(json);
      }
    );
    
    await app.on('change', async () => {
      const headerRow = await buildApiArray(
        await app.models[0].then((result) => {
          return result['attributes']['data'];
        })
        , this.props.appFunctions.addTimerList);
      await convertApiDataToSheetAsRange(
        sortArray(headerRow, app.apiParams.apiBoostedFields, true),
        app.apiParams.apiBoostedFields,
        'Object',
        'App_' + appId + '_i',
        '',
        appFunctions.setAppState
      );
    });
    await app.fetch(appFunctions);
  };
  
  removeAppsList = async () => {
    await localStorage.removeItem('appsModel');
  };
  
  getAppType = async (appId: any, authParams: AuthParams, appFunctions, setLocalState): Promise<string> => {
    let appsList;
    if (appFunctions.getStateData('listExists', 'appsList')) {
      appsList = appFunctions.getListValue('appsList', 'identifier', appFunctions.getStateData('stateVal', 'adminName'));
    }
    
    if (!appsList) {
      await getAppsList(true, authParams, appFunctions, setLocalState);
    }
    
    async function search(nameKey, myArray) {
      let appType;
      for (let i = 0; i < (await myArray.length); i++) {
        if ((await myArray[i].attributes.data.Id.Value) === nameKey) {
          appType = await myArray[i].attributes.data.AppType;
        }
      }
      return appType;
    }
    
    const appType = await search(parseInt(appId), appFunctions.getStateData('stateVal', 'apps'));
    if (appType.toString() === 'Internal') {
      return 'Internal';
    } else if (appType.toString() === 'Public') {
      return 'Public';
    } else {
      return 'NotFound';
    }
  };
  
  getSelectedApp = async (authParams: AuthParams, appFunctions, setLocalState) => {
    let appsList;
    if (appFunctions.getStateData('listExists', 'appsList')) {
      appsList = appFunctions.getListValue('appsList', 'identifier', appFunctions.getStateData('stateVal', 'adminName'));
    }
    
    if (!appsList) {
      await getAppsList(true, authParams, appFunctions, setLocalState);
    }
    
    await Excel.run(async (context) => {
      let range = context.workbook.getSelectedRange();
      range.load('address');
      range.load('values');
      await context.sync();
      let appArr = this.getAppVals(range.address, 2);
      let sheet = context.workbook.worksheets.getActiveWorksheet();
      let appTypeArr = [];
      for (const range of appArr) {
        let appTypeRange = await sheet.getRange(range);
        await appTypeRange.load('values');
        await context.sync();
        appTypeArr.push(appTypeRange.values[0][0]);
      }
      
      return await context.sync().then(async () => {
        range.values.map(async (value, index) => {
          if (value[0]) {
            let appType = appTypeArr[index];
            if (appType.toString() === 'Internal') {
              await this.getInternalApp(value[0], authParams, appFunctions);
            } else if (appType.toString() === 'Public') {
              await this.getPublicApp(value[0], authParams, appFunctions, setLocalState);
            } else {
              return;
            }
          }
        });
      });
    }).catch((e) => {
      appFunctions.setAppState({ errorMessage: e.toString() });
    });
  };
  
  getDeviceAppsList = async (authParams: AuthParams, appId: string, appType: string, appDevicesList, appFunctions, setLocalState) => {
    let deviceAppsList = [];
    let modelLength = appDevicesList.list.length;
    if (appDevicesList.list.length > 0) {
      let batchSize = 100;
      let blockingSince = performance.now();
      for (let j = 0; j < modelLength; modelLength < j + batchSize ? j = modelLength - j : j = j + batchSize) {
        
        let gets = [];
        for (let i = j; i < (modelLength < j + batchSize ? j + (modelLength - j) : j + batchSize); i++) {
          
          // do not add an await here or it breaks.
          gets.push(getDeviceApps(appDevicesList.list[i], authParams, appFunctions, setLocalState));
        }
        await Promise.all(gets)
          .then(async () => {
            if (await appFunctions.getStateData('stateVal', 'break') === true) {
              j = modelLength;
              await appFunctions.setAppState({ break: false });
            }
            appFunctions.addToAllValues('currentAppDevices', {
              identifier: 'status',
              value: {
                currentPage: j,
                totalPages: modelLength
              }
            });
            setLocalState({ listStatus: { ['currentAppDevices']: appFunctions.getStateData('value', 'currentAppDevices') } });
            
            let now: number = Date.now();
            if (now - blockingSince > 10) {
              await new Promise(resolve => setImmediate(resolve));
              blockingSince = now;
            }
          });
      }
      
      
      for (let i = 0; i < modelLength; i++) {
        
        if (await appFunctions.getStateData('stateVal', 'break') === true) {
          i = modelLength;
          await appFunctions.setAppState({ break: false });
        }
        let deviceId = appDevicesList.list[i];
        let deviceApps;
        
        deviceApps = await appFunctions.getListValue('deviceAppsList', 'identifier', deviceId);
        
        if (deviceApps && deviceApps.length === 1) {
          let app = deviceApps[0].list.filter((obj) => {
            if (obj['Id']) {
              return obj['Id']['Value'] === parseInt(appId) && obj['Type'] === appType;
            } else {
              return false;
            }
          });
          if (app && app.length === 1) {
            deviceAppsList = [
              {
                DeviceId: deviceId,
                AppStatus: app[0]['Status'],
                AppIsManaged: app[0]['IsManaged'],
                AppVersion: app[0]['Version'],
                AppBuildVersion: app[0]['BuildVersion']
              }, ...deviceAppsList
            ];
          }
        }
        let now: number = Date.now();
        if (now - blockingSince > 10) {
          await new Promise(resolve => setImmediate(resolve));
          blockingSince = now;
        }
      }
      await appFunctions.addToAllValues('currentAppDevices', {});
      setLocalState({ listStatus: { ['currentAppDevices']: {} } });
      await appFunctions.addToAllLists(appType.toLowerCase() + 'AppDevicesList', {
        identifier: appId,
        list: deviceAppsList
      });
    }
    return deviceAppsList;
  };
  
  
  getInternalAppDevices = async (appId, authParams: AuthParams, appFunctions: AppFunctions, setLocalState) => {
    let path = ['mam', 'apps', 'internal', appId, 'devices'];
    let queryParams = { pagesize: 5000 };
    let apiReturnType: ApiReturnType = 'Array';
    let apiReturnValue = 'DeviceId';
    let apiBoostedFields = [];
    let localOnly = true;
    let sheetTitle = '';
    let addOnDataType = '';
    let listName = 'internalApp' + appId + 'DevicesList';
    let identifier = appId;
    let updateLocalState = true;
    
    
    setLocalState({ listStatus: { 'gettingDeviceIds': true } });
    
    let internalAppsDevices = await genericListFunc(
      authParams,
      path,
      queryParams,
      apiReturnType,
      apiReturnValue,
      apiBoostedFields,
      localOnly,
      sheetTitle,
      addOnDataType,
      listName,
      identifier,
      updateLocalState,
      appFunctions,
      setLocalState
    );
    // setLocalState({ listStatus: {} });
    
    let deviceAppsList = [];
    if (internalAppsDevices[0].list.length > 0) {
      deviceAppsList = await this.getDeviceAppsList(authParams, appId, 'Internal', internalAppsDevices[0], appFunctions, setLocalState);
      if (deviceAppsList.length > 0) {
        await convertApiDataToSheetAsRange(
          sortArray(await buildApiArray(deviceAppsList, this.props.appFunctions.addTimerList), [], true),
          [],
          'Array',
          'App_' + appId + '_i',
          'Devices',
          appFunctions.setAppState
        );
      } else {
        appFunctions.setAppState({ errorMessage: 'No Data Received' });
      }
      
    } else {
      appFunctions.setAppState({ errorMessage: 'No Data Received' });
    }
  };
  
  getPublicAppDevices = async (appId, authParams: AuthParams, appFunctions: AppFunctions, setLocalState) => {
    let path = ['mam', 'apps', 'public', appId, 'devices'];
    let queryParams = { pagesize: 5000 };
    let apiReturnType: ApiReturnType = 'Array';
    let apiReturnValue = 'DeviceId';
    let apiBoostedFields = [];
    let localOnly = true;
    let sheetTitle = '';
    let addOnDataType = '';
    let listName = 'publicApp' + appId + 'DevicesList';
    let identifier = appId;
    let updateLocalState = true;
    
    setLocalState({ listStatus: { 'gettingDeviceIds': true } });
    let publicAppsDevices = await genericListFunc(
      authParams,
      path,
      queryParams,
      apiReturnType,
      apiReturnValue,
      apiBoostedFields,
      localOnly,
      sheetTitle,
      addOnDataType,
      listName,
      identifier,
      updateLocalState,
      appFunctions,
      setLocalState
    );
    // setLocalState({ listStatus: {} });
    
    let deviceAppsList = [];
    if (publicAppsDevices[0].list.length > 0) {
      deviceAppsList = await this.getDeviceAppsList(authParams, appId, 'Public', publicAppsDevices[0], appFunctions, setLocalState);
      
      if (deviceAppsList.length > 0) {
        await convertApiDataToSheetAsRange(
          sortArray(await buildApiArray(deviceAppsList, this.props.appFunctions.addTimerList), [], true),
          [],
          'Array',
          'App_' + appId + '_p',
          'Devices',
          appFunctions.setAppState
        );
      } else {
        appFunctions.setAppState({ errorMessage: 'No Data Received' });
      }
      
    } else {
      appFunctions.setAppState({ errorMessage: 'No Data Received' });
    }
    
  };
  
  getCurrentAppDevices = async (AuthParams, appFunctions: AppFunctions, setLocalState) => {
    // this.showAppDeviceProgress=true;
    let currentTab = await getCurrentTab();
    if (currentTab[0] === 'App') {
      if (currentTab[2] === 'i') {
        await this.getInternalAppDevices(currentTab[1], AuthParams, appFunctions, setLocalState);
      } else if (currentTab[2] === 'p') {
        await this.getPublicAppDevices(currentTab[1], AuthParams, appFunctions, setLocalState);
      }
    }
    
  };
  
  setLocalState = async (newStateData: {}) => {
    this.setState(newStateData);
  };
  
  
  render() {
    const {
      appFunctions
    } = this.props;
    const authParams = {
      url: appFunctions.getStateData('stateVal', 'url'),
      adminName: appFunctions.getStateData('stateVal', 'adminName'),
      password: appFunctions.getStateData('stateVal', 'password'),
      apikey: appFunctions.getStateData('stateVal', 'apikey'),
      domain: appFunctions.getStateData('stateVal', 'domain')
    };
    
    // console.log(JSON.stringify(this.state.listStatus));
    // const textvariants = [
    //   { name: 'tiny' },
    //   { name: 'xSmall' },
    //   { name: 'small' },
    //   { name: 'smallPlus' },
    //   { name: 'medium' },
    //   { name: 'mediumPlus' },
    //   { name: 'large' },
    //   { name: 'xLarge' },
    //   { name: 'xxLarge' },
    //   { name: 'mega' },
    // ];
    let getAppListBody, gettingDeviceIds, getCurrentAppDevicesStatusBody;
    if (this.state.listStatus['appsList'] && JSON.stringify(this.state.listStatus['appsList']) !== '{}') {
      getAppListBody = (
        <MessageBar>
          Getting {this.state.listStatus['appsList'] && JSON.stringify(this.state.listStatus['appsList']) !== '{}' ? this.state.listStatus['appsList']['value']['currentPage'] + ' page of ' : null}
          of {this.state.listStatus['appsList'] && JSON.stringify(this.state.listStatus['appsList']) !== '{}' ? this.state.listStatus['appsList']['value']['totalPages'] + ' pages.' : null}
          <CommandButton style={{ fontSize: 'small' }} iconProps={{ iconName: 'Stop' }}
                         onClick={() => appFunctions.setAppState({ break: true })}>Stop</CommandButton>
        </MessageBar>
      );
    } else {
      getAppListBody = null;
    }
    
    if (this.state.listStatus['gettingDeviceIds'] && JSON.stringify(this.state.listStatus['gettingDeviceIds']) !== '{}') {
      gettingDeviceIds = (
        <MessageBar>
          Getting Device Id's to process.
        </MessageBar>
      );
    } else {
      gettingDeviceIds = null;
    }
    
    if (this.state.listStatus['currentAppDevices'] && JSON.stringify(this.state.listStatus['currentAppDevices']) !== '{}') {
      getCurrentAppDevicesStatusBody = (
        <MessageBar>
          Getting {this.state.listStatus['currentAppDevices'] && JSON.stringify(this.state.listStatus['currentAppDevices']) !== '{}' ? this.state.listStatus['currentAppDevices']['value']['currentPage'] + ' device of ' : null}
          of {this.state.listStatus['currentAppDevices'] && JSON.stringify(this.state.listStatus['currentAppDevices']) !== '{}' ? this.state.listStatus['currentAppDevices']['value']['totalPages'] + ' devices.' : null}
          <CommandButton style={{ fontSize: 'small' }} iconProps={{ iconName: 'Stop' }}
                         onClick={() => appFunctions.setAppState({ break: true })}>Stop</CommandButton>
        </MessageBar>
      );
    } else {
      getCurrentAppDevicesStatusBody = null;
    }
    
    
    const stackTokens = { childrenGap: 5, maxWidth: 300 };
    return <Stack tokens={stackTokens}>
      <br />
      <Stack.Item align='center'>
        <h3>Apps Page</h3>
      </Stack.Item>
      <p>
        Choose to get a list of your Apps or select an App Id on your list and
        get various details about your App
      </p>
      <Stack.Item align='center'>
        <DefaultButton
          type='submit'
          className='ms-welcome__action'
          buttonType={ButtonType.hero}
          iconProps={{ iconName: 'ChevronRight' }}
          onClick={() => {
            setTimeout(() => {
              this.setState({ listStatus: { ['appsList']: this.props.appFunctions.getStateData('value', 'appsList') } });
            }, 100);
            getAppsList(false, authParams, appFunctions, this.setLocalState).then(() => {
                this.setState({ listStatus: { ['appsList']: this.props.appFunctions.getStateData('value', 'appsList') } });
              }
            );
          }}
        >
          Get Apps List
        </DefaultButton>
        {getAppListBody}
      </Stack.Item>
      <Stack.Item align='center'>
        <DefaultButton
          type='submit'
          className='ms-welcome__action'
          buttonType={ButtonType.hero}
          iconProps={{ iconName: 'ChevronRight' }}
          onClick={() => this.getSelectedApp(authParams, appFunctions, this.setLocalState)}
        >
          Get Selected App(s) Data
        </DefaultButton>
      </Stack.Item>
      <Stack.Item align='center'>
        <DefaultButton
          type='submit'
          className='ms-welcome__action'
          buttonType={ButtonType.hero}
          iconProps={{ iconName: 'ChevronRight' }}
          onClick={() => {
            this.getCurrentAppDevices(authParams, appFunctions, this.setLocalState).then(() => {
                setTimeout(() => {
                  this.setState({ listStatus: { ['currentAppDevices']: this.props.appFunctions.getStateData('value', 'currentAppDevices') } });
                }, 2000);
              }
            );
          }}
        >
          Get Current App Devices
        </DefaultButton>
        {gettingDeviceIds}
        {getCurrentAppDevicesStatusBody}
      </Stack.Item>
      <Stack.Item align='center'>
        <DefaultButton
          className='ms-welcome__action'
          buttonType={ButtonType.hero}
          iconProps={{ iconName: 'ChevronRight' }}
          onClick={this.removeAppsList}
        >
          Remove List
        </DefaultButton>
      </Stack.Item>
    
    </Stack>;
  }
}
