export const getAdminsList = async (
  authParams: AuthParams,
  appFunctions: AppFunctions,
  setLocalState
) => {
  // appFunctions.setAppState({ appStatus: 'loginInProcess' });
  
  let path = ['system', 'admins', 'search'];
  let queryParams = { 'username': authParams.adminName };
  let apiReturnType: ApiReturnType = 'Array';
  let apiReturnValue = 'Admins';
  let apiBoostedFields = ['Id'];
  let localOnly = true;
  let sheetTitle = 'CurrentAdmin';
  let addOnDataType = '';
  let listName = 'currentAdmin';
  let identifier = authParams.adminName.toLowerCase();
  let updateLocalState = true;
  
  let currentAdmin = await genericListFunc(
    authParams,
    path,
    queryParams,
    apiReturnType,
    apiReturnValue,
    apiBoostedFields,
    localOnly,
    sheetTitle,
    addOnDataType,
    listName,
    identifier,
    updateLocalState,
    appFunctions,
    setLocalState
  );
  if (currentAdmin && currentAdmin[0].list.length > 0) {
    let groupList = [];
    groupList = await getGroupList(authParams, appFunctions, setLocalState);
    let myGroup = groupList[0].list.filter(obj => {
      return obj['LocationGroupId'].toString() === currentAdmin[0].list[0]['LocationGroupId'].toString();
    });
    
    if (!myGroup[0]) {
      groupList[0].list.push({
        LocationGroupId: currentAdmin[0].list[0]['LocationGroupId'],
        LocationGroupName: currentAdmin[0].list[0]['LocationGroup'],
        TotalDevices: 0
      });
      await appFunctions.deleteValOrList('list', 'groupList');
      appFunctions.addToAllLists('groupList', {
        identifier: authParams.adminName.toLowerCase(),
        list: groupList[0].list
      });
    }
    
    await appFunctions.setAppState({
      password: authParams.password,
      adminName: authParams.adminName,
      apikey: authParams.apikey,
      domain: authParams.domain,
      roles: getAdminRoles(currentAdmin[0].list[0]['Id']['Value'].toString(), authParams, appFunctions),
      firstname: currentAdmin[0].list[0]['FirstName'],
      lastname: currentAdmin[0].list[0]['LastName'],
      emailAddress: currentAdmin[0].list[0]['Email'],
      groupid: currentAdmin[0].list[0]['LocationGroupId'],
      groupidName: currentAdmin[0].list[0]['LocationGroup'],
      groupidUUID: currentAdmin[0].list[0]['OrganizationGroupUuid']
    });
    
    if (!localOnly) {
      // await convertApiDataToSheetAsTable(
      //   sortArray(
      //     await buildApiArray(models),
      //     admins.apiParams.apiBoostedFields,false
      //   ),
      //   admins.apiParams.apiBoostedFields,
      //   'Array',
      //   'Admins',
      //   '',
      //   this.props.displayError
      // );
    }
    appFunctions.setAppState({ appStatus: 'LoggedIn' });
  } else {
    appFunctions.setAppState({ appStatus: 'notLoggedIn' });
  }
  // });
  // await admins.fetch(appFunctions.setAppState);
};

