export const getAdminRoles = async (adminId: string, authParams: AuthParams, appFunctions: AppFunctions) => {
  let apiParams: ApiParams = {
    authParams: authParams,
    queryParams: {},
    path: ['system', 'admins', adminId],
    apiReturnType: 'Array',
    apiReturnValue: 'Roles',
    apiBoostedFields: []
  };
  
  const roles = new ApiCollection(
    apiParams,
    async (json: GenericListApiProps) => {
      return await GenericListApi.buildObject(json);
    }
  );
  
  await roles.on('change', async () => {
    let models = [];
    
    await roles.models.map((data) => (
      data.then((result) => (
        models.push(result['attributes']['data'])
      ))
    ));
    return models;
  });
  await roles.fetch(appFunctions);
  return roles;
};
