import * as React from 'react';

import { MailTo, MainPageBodyProps } from '../Components';
import { Stack } from 'office-ui-fabric-react/lib/Stack';

export interface LoggedInPageBodyState {
  listStatus?: {};
}

export class LoggedInPageBody extends React.Component<MainPageBodyProps,
  LoggedInPageBodyState> {
  constructor(props) {
    super(props);
    this.state = {
      listStatus: {}
    };
    
    this.handleChangeGroupId = this.handleChangeGroupId.bind(this);
  }
  
  handleChangeGroupId(event) {
    this.props.appFunctions.setAppState({ groupid: event.target.value });
  }
  
  setLocalState = async (newStateData: {}) => {
    this.setState(newStateData);
  };
  
  render() {
    //password Announced6warn
    const stackTokens = { childrenGap: 0, maxWidth: 300 };
    return (
      <Stack tokens={stackTokens}>
        <br/>
        <Stack.Item align='center'>
          <h4>Welcome to the the app {this.props.appFunctions.getStateData('stateVal', 'firstname')}</h4>
        </Stack.Item>
        <div>
        
        </div>
        <p>
          What would you like to work with? Choose the from the menu above to be
          taken to the appropriate page.
        </p>
        <p>
          Each page has various opti ons to choose from. If you find something
          missing which you see in the Workspace ONE console,{' '}
          <MailTo
            email='lettol@vmware.com'
            subject='Excel Add-In request'
            body='What would you like to know?'
          >
            let us know
          </MailTo>
        </p>
      </Stack>
    );
  }
}
