export const genericListFunc = async (
  authParams: AuthParams,
  path: string[],
  queryParams: {},
  apiReturnType: ApiReturnType,
  apiReturnValue: string,
  apiBoostedFields: string[],
  localOnly: boolean,
  sheetTitle: string,
  addOnDataType: string,
  listName: string,
  identifier: string,
  updateLocalState: boolean,
  appFunctions: AppFunctions,
  setLocalState: (newStateData: {}) => {}
): Promise<any[]> => {
  let apiParams: ApiParams = {
    authParams: authParams,
    queryParams: queryParams,
    path: path,
    apiReturnType: apiReturnType,
    apiReturnValue: apiReturnValue,
    apiBoostedFields: apiBoostedFields
  };
  
  let result = [];
  if (await appFunctions.getStateData('stateVal', 'useCache')) {
    if (await appFunctions.getStateData('listExists', listName)) {
      result = await appFunctions.getListValue(
        listName,
        'identifier',
        identifier
      );
    }
  }
  if (result && result.length > 0) {
    if (!localOnly) {
      let groupId = await appFunctions.getStateData('stateVal', 'groupid');
      await convertApiDataToSheetAsRange(
        sortArray(
          await buildApiArray(
            appFunctions.getStateData('stateVal', 'filterOnGroupId')
              ? result[0].list.filter((obj) => {
                switch (sheetTitle) {
                  case 'Devices':
                    return (
                      parseInt(obj['LocationGroupId']['Id']['Value']) ===
                      parseInt(groupId)
                    );
                  // case 'Apps':
                  //   return parseInt(obj['LocationGroupId']) === parseInt(groupId);
                  // case 'Profiles':
                  //   return parseInt(obj['OrganizationGroupId']) === parseInt(groupId);
                  // case 'Baselines':
                  //   return parseInt(obj['rootLocationGroupID']) === parseInt(groupId);
                  default:
                    return obj;
                }
              })
              : result[0].list,
            appFunctions.addTimerList
          ),
          apiBoostedFields,
          true
        ),
        apiBoostedFields,
        'Array',
        sheetTitle,
        addOnDataType,
        appFunctions.setAppState
      );
    }
    return result;
  } else {
    let models = [];
    let genericList = new ApiCollectionP(
      apiParams,
      async (json: GenericListApiProps) => {
        return await GenericListApi.buildObject(json);
      }
    );
    await genericList.on('change', async () => {
      let blockingSince: number = Date.now();
      if (updateLocalState) {
        await appFunctions.addToAllValues(listName, {
          identifier: 'status',
          value: {
            currentPage: genericList.page + 1,
            totalPages: genericList.numberOfPages
          }
        });
        setLocalState({
          listStatus: {
            [listName]: appFunctions.getStateData('value', listName)
          }
        });
      }
      await genericList.models.map(async (object) => {
        object.then(async (result) => {
          models = [await result['attributes']['data'], ...models];
          
        });
      });
      let now: number = Date.now();
      if (now - blockingSince > 10) {
        await new Promise((resolve) => setImmediate(resolve));
      }
    });
    await genericList.on('additional', async () => {
      let blockingSince: number = Date.now();
      if (updateLocalState) {
        await appFunctions.addToAllValues(listName, {
          identifier: 'status',
          value: {
            currentPage: genericList.page + 1,
            totalPages: genericList.numberOfPages
          }
        });
        setLocalState({
          listStatus: {
            [listName]: appFunctions.getStateData('value', listName)
          }
        });
      }
      
      if (genericList.models) {
        await genericList.models.map(async (object) => {
          object.then(async (result) => {
            models = [await result['attributes']['data'], ...models];
            
          });
        });
      }
      let now: number = Date.now();
      if (now - blockingSince > 10) {
        await new Promise((resolve) => setImmediate(resolve));
      }
    });
    await genericList.on('finished', async () => {
      if (updateLocalState) {
        appFunctions.deleteValOrList('value', listName);
        setLocalState({ listStatus: { [listName]: {} } });
      }
      return {
        identifier: identifier,
        list: models
      };
    });
    if (updateLocalState) {
      await appFunctions.addToAllValues(listName, {
        identifier: 'status',
        value: {
          currentPage: 1,
          totalPages: '...'
        }
      });
    }
    return await genericList.fetch(appFunctions).then(async () => {
      // console.log(models);
      
      return await new Promise((resolve) => setImmediate(resolve)).then(
        async () => {
          return await appFunctions
            .addToAllLists(listName, { identifier: identifier, list: models })
            .then(async () => {
              if (!localOnly) {
                let groupId = await appFunctions.getStateData(
                  'stateVal',
                  'groupid'
                );
                await convertApiDataToSheetAsRange(
                  sortArray(
                    await buildApiArray(
                      appFunctions.getStateData('stateVal', 'filterOnGroupId')
                        ? models.filter((obj) => {
                          switch (sheetTitle) {
                            case 'Devices':
                              return (
                                parseInt(
                                  obj['LocationGroupId']['Id']['Value']
                                ) === parseInt(groupId)
                              );
                            // case 'Apps':
                            //   return parseInt(obj['LocationGroupId']) === parseInt(groupId);
                            // case 'Profiles':
                            //   return parseInt(obj['OrganizationGroupId']) === parseInt(groupId);
                            // case 'Baselines':
                            //   return parseInt(obj['rootLocationGroupID']) === parseInt(groupId);
                            default:
                              return obj;
                          }
                        })
                        : models,
                      appFunctions.addTimerList
                    ),
                    apiBoostedFields,
                    true
                  ),
                  apiBoostedFields,
                  'Array',
                  sheetTitle,
                  addOnDataType,
                  appFunctions.setAppState
                );
              }
              return [
                {
                  identifier: identifier,
                  list: models
                }
              ];
            });
        }
      );
    });
  }
};
