export const genericObjectFunc = async (
  authParams: AuthParams,
  path: string[],
  queryParams: {},
  apiReturnType: ApiReturnType,
  apiReturnValue: string,
  apiBoostedFields: string[],
  localOnly: boolean,
  sheetTitle: string,
  addOnDataType: string,
  valueName: string,
  appFunctions: AppFunctions
): Promise<any[]> => {
  let apiParams: ApiParams = {
    authParams: authParams,
    queryParams: queryParams,
    path: path,
    apiReturnType: apiReturnType,
    apiReturnValue: apiReturnValue,
    apiBoostedFields: apiBoostedFields,
  };

  let result;
  if (await appFunctions.getStateData('stateVal', 'useCache')) {
    if (appFunctions.getStateData('valueExists', valueName)) {
      result = appFunctions.getStateData('value', valueName);
    }
  }
  if (result) {
    if (!localOnly) {
      await convertApiDataToSheetAsRange(
        sortArray(
          await buildApiArray(result, appFunctions.addTimerList),
          apiBoostedFields,
          true
        ),
        apiBoostedFields,
        'Object',
        sheetTitle,
        addOnDataType,
        appFunctions.setAppState
      );
    }
    return result;
  } else {
    let genericObject = new ApiCollectionP(
      apiParams,
      async (json: SingleObjectApiProps) => {
        return await SingleObjectApi.buildObject(json);
      }
    );
    await genericObject.on('change', async () => {
      await genericObject.models[0].then(async (result) => {
        return await result['attributes']['data'];
      });
    });
    return await genericObject.fetch(appFunctions).then(async () => {
      return genericObject.models[0].then(async (model) => {
        appFunctions.deleteValOrList('value', valueName);
        appFunctions.addToAllValues(valueName, model['attributes']['data']);

        if (!localOnly) {
          await convertApiDataToSheetAsRange(
            await sortArray(
              await buildApiArray(
                model['attributes']['data'],
                appFunctions.addTimerList
              ),
              apiBoostedFields,
              true
            ),
            apiBoostedFields,
            'Object',
            sheetTitle,
            addOnDataType,
            appFunctions.setAppState
          );
        }
        return model['attributes']['data'];
      });
      // await appFunctions.deleteValOrList('value', valueName);
      // await appFunctions.addToAllValues(valueName, await thisObject);
      //
      // if (!localOnly) {
      //   await convertApiDataToSheetAsRange(
      //     sortArray(
      //       await buildApiArray(thisObject, appFunctions.addTimerList),
      //       apiBoostedFields,
      //       true
      //     ),
      //     apiBoostedFields,
      //     'Object',
      //     sheetTitle,
      //     addOnDataType,
      //     appFunctions.setAppState
      //   );
      // }
      // return thisObject;
    });
  }
};
