// import {rotateArray, rotateArray2} from './miscFunctions';

export const sortArray = (
  origArray: any[] | any[][],
  apiBoostedFields: string[],
  rotate: boolean
): string[][] => {
  // Turned this into an async function by waiting for the Promise before continueing
  let thisArray: string[][] = origArray;

  // First you need pull the Boosted Fields to the front of the array
  for (let i = 0; i < apiBoostedFields.length; i++) {
    for (let j = i; j < thisArray.length; j++) {
      let match1;
      if (thisArray[j][0]) {
        match1 =
          thisArray[j][0].startsWith(apiBoostedFields[i]) &&
          thisArray[j][0].length === apiBoostedFields[i].length;

        if (match1) {
          let temp = thisArray[j];
          thisArray[j] = thisArray[i];
          thisArray[i] = temp;
        }
      }
    }
  }

  // Next Pull the fields together which are related
  let tempArray: string[][];
  tempArray = [];
  for (let i = 0; i < apiBoostedFields.length; i++) {
    tempArray.push(thisArray[i]);
  }
  for (let x = apiBoostedFields.length; x < thisArray.length; x++) {
    let curHeader = thisArray[x][0];
    tempArray.push(thisArray[x]);
    for (let y = x + 1; y < thisArray.length; y++) {
      // console.log(12);
      let match;
      if (thisArray[y][0]) {
        match =
          thisArray[y][0].startsWith(curHeader) &&
          thisArray[y][0].indexOf(':') === curHeader.length;

        if (match) {
          tempArray.push(thisArray[y]);
          thisArray.splice(y, 1);
        }
      }
    }
  }

  let getArray2d = function (a, x, y) {
    return a[y][x];
  };
  let newarr = [];
  if (rotate) {
    if (tempArray[0]) {
      tempArray[0].forEach(() => newarr.push(new Array(tempArray.length)));
      for (var i = 0; i < newarr.length; i++) {
        for (var j = 0; j < newarr[0].length; j++) {
          newarr[i][j] = getArray2d(tempArray, i, j);
        }
      }
      return newarr;
    } else {
      return [];
    }
    // tempArray.reverse();
    // rotateArray(tempArray);
  } else {
    return tempArray;
  }


};
