import * as React from 'react';

export interface MailToProps {
  email: string,
  subject?: string,
  body?: string
}

export class MailTo extends React.Component<MailToProps> {

  render() {
    const {email, subject, body} = this.props;

    return (
      <a
        href={`mailto:${email}?subject=${encodeURIComponent(subject) || ''}&body=${encodeURIComponent(body) || ''}`}>{this.props.children}</a>
    );
  }
}
