/*
    This component is needed to wrap the Fabric React MessageBar when the latter appears at the
    top of the task pane in an Office Add-in, because the taskpane in an Office Add-in has a
    personality menu that covers a small rectangle of the upper right corner of the task pane.
    This rectangle covers the "dismiss X" on the right end of the MessageBar unless extra padding
    is added.
*/

import * as React from 'react';
import {ProgressIndicator} from '@fluentui/react';

export interface ProgressBarProps {
  itemType:string;
  totalItems?:number;
  currentItem?: number;
}

export class ProgressBar extends React.Component<ProgressBarProps> {

  constructor(props: ProgressBarProps) {
    super(props);
  }

  showProgress=null;

  description = ():string => {
    if (this.props.currentItem && this.props.totalItems){
      this.showProgress = this.props.currentItem/this.props.totalItems;
      return 'Getting '+this.props.itemType+' '+this.props.currentItem+' of '+this.props.totalItems
    }else if (this.props.currentItem){
      return 'Getting '+this.props.itemType+' '+this.props.currentItem
    }else if (this.props.totalItems){
      return 'Getting '+this.props.totalItems+' '+this.props.itemType+'(s)'
    } else {
      return 'Getting '+this.props.itemType+'(s)'
    }
  }

  render() {
    return (
        <ProgressIndicator label="Progress" description="Getting 5 of 50"
                           percentComplete={this.showProgress} />
      )
  }
}
