import * as React from 'react';
import { Spinner, SpinnerSize, Fabric } from '@fluentui/react';
import {
  AppFunctions,
  AppProps,
  AppState,
  GenericList,
  Header,
  TimerList, typeStrings
} from './index';
import {
  MainPageBody,
  StartPageBody,
  LoginPageBody,
  SuccessPageBody,
  OfficeAddinMessageBar
  
} from '../Pages';
import { leon } from '../secrets';
import { ENV } from '../environment';


export default class App extends React.Component<AppProps, AppState> {
  constructor(props, context) {
    super(props, context);
    this.state = {
      url: `https://${window.location.host}/wsoneapi`,
      env: ENV,
      adminName: '',
      password: '',
      apikey: '',
      firstname: '',
      lastname: '',
      emailAddress: '',
      userUUID: '',
      userId: '',
      groupidName: '',
      groupidUUID: '',
      domain: '',
      groupid: '',
      filterOnGroupId: false,
      appStatus: 'notLoggedIn',
      tabSelected: '',
      headerMessage: 'Welcome',
      errorMessage: '',
      currentPage: null,
      totalPages: null,
      allValues: {},
      allLists: {},
      timerList: [],
      useCache: true,
      break: false
    };
    
    // Bind the methods that we want to pass to, and call in, a separate
    // module to this component. And rename setState to boundSetState
    // so code that passes boundSetState is more self-documenting.
  }
  
  
  // Runs when the user clicks the X to close the message bar where
  // the error appears.
  errorDismissed = () => {
    this.setState({ errorMessage: '' });
    // If the error occured during a "in process" phase (logging in or getting files),
    // the action didn't complete, so return the UI to the preceding state/view.
    this.setState((prevState) => {
      if (prevState.appStatus === 'loginInProcess') {
        return { appStatus: 'notLoggedIn' };
      }
      return null;
    });
  };
  
  addToAllLists = async (listType: string, list: GenericList) => {
    let start = performance.now();
    await this.setState((state) => {
      if (state.allLists[listType]) {
        // console.log('add');
        state.allLists[listType] = [list, ...state.allLists[listType]];
      } else {
        // console.log('new');
        // state.allLists[listType] = [];
        state.allLists[listType] = [list];
      }
      // console.log(state.allLists);
    });
    let funcTime = performance.now() - start;
    await this.addTimerList({ functionName: 'addToAllLists', totalTime: funcTime });
  };
  
  addToAllValues = async (valueName: string, value: any) => {
    let start = performance.now();
    this.setState((state) => {
      state.allValues[valueName] = value;
      // console.log(`valueName: ${valueName}
      // value:`);
      // console.log(value);
      // console.log(state.allValues);
    });
    let funcTime = performance.now() - start;
    await this.addTimerList({ functionName: 'addToAllValues', totalTime: funcTime });
  };
  
  deleteValOrList = async (type: 'value' | 'list', value: string) => {
    if (type === 'value') {
      delete this.state.allValues[value];
    } else if (type === 'list') {
      delete this.state.allLists[value];
    }
  };

// Use this instead of passing stateData
  getStateData = (type: typeStrings, name: string): any | GenericList | boolean => {
    // console.log(type);
    // console.log(name);
    if (type === 'stateVal') {
      // console.log(`returning value: ${this.state[name]}`);
      return this.state[name];
    } else if (type === 'value') {
      // console.log(`returning value: ${this.state[name]}`);
      return this.state.allValues[name] || null;
    } else if (type === 'valueExists') {
      // console.log(`returning value: ${this.state[name]}`);
      return !!this.state.allValues[name];
    } else if (type === 'list') {
      // console.log(`returning list:`);
      // console.log(this.state.allLists[name]);
      return this.state.allLists[name];
    } else if (type === 'listExists') {
      // console.log(`returning Whether List exists: ${!!this.state.allLists[name]}`);
      return !!this.state.allLists[name];
    }
  };

//use this instead of filtering on the whole list when passed in stateData
  getListValue = async (listName: string, filterValName: string, filterVal: string) => {
    return this.state.allLists[listName].filter((obj) => {
      return obj[filterValName] === filterVal;
    });
  };
  
  addTimerList = async (list: TimerList) => {
    if (this.getStateData('listExists', 'timerList')) {
      let curIndex = -1;
      this.state.allLists['timerList'].filter((obj, index) => {
        if (obj.functionName === list.functionName) {
          curIndex = index;
        }
      });
      if (curIndex > -1) {
        this.state.allLists['timerList'][curIndex].totalTime = this.state.allLists['timerList'][curIndex].totalTime + list.totalTime;
      }
    } else {
      this.state.allLists['timerList'] = [];
      this.state.allLists['timerList'] = [list, ...this.state.allLists['timerList']];
    }
    // console.log(this.state.allLists['timerList']);
  };
  
  logout = async () => {
    // TODO: Once you go live or in public, set the state back to default
    this.setState({
      adminName: '',
      password: '',
      apikey: '',
      firstname: '',
      lastname: '',
      emailAddress: '',
      userUUID: '',
      userId: '',
      groupidName: '',
      groupidUUID: '',
      domain: '',
      groupid: '',
      appStatus: 'notLoggedIn',
      tabSelected: '',
      headerMessage: 'Welcome',
      errorMessage: '',
      allValues: {} = {},
      allLists: {} = {},
      timerList: [] = [],
      useCache: true
    });
    this.setState({ appStatus: 'notLoggedIn' });
  };
  
  setAppState = async (newStateData: {}) => {
    this.setState(newStateData);
    // console.log(this.state);
  };
  
  appFunctions: AppFunctions = {
    addToAllLists: this.addToAllLists,
    addToAllValues: this.addToAllValues,
    getStateData: this.getStateData,
    getListValue: this.getListValue,
    setAppState: this.setAppState,
    addTimerList: this.addTimerList,
    deleteValOrList: this.deleteValOrList,
    logout: this.logout
  };
  
  
  render() {
    const { isOfficeInitialized } = this.props;
    
    if (!isOfficeInitialized) {
      return null;
      // (
      // <div className='ms-welcome'>
      //   <Header
      //     logo='assets/WSONE_icon_80x80px.jpg'
      //     title={title}
      //     message={'Initializing...'}
      //   />
      //   <Spinner
      //     size={SpinnerSize.large}
      //   />
      // </div>
      // <Progress
      //   title={title}
      //   logo='assets/WSONE_icon_80x80px.jpg'
      //   message='Please sideload your add-in to see app body.'
      // />
      // );
    }
    
    if (leon.myvar) {
    }
    
    // Set the body of the page based on where the user is in the workflow.
    let body;
    
    // This next section is equivalent to a router in a traditional app.  The AppStatus is available
    // as part of the app state and determines which page is included in the body of the Office Add-in
    if (this.state.appStatus === 'notLoggedIn') {
      body = (
        <StartPageBody
          setAppState={this.setAppState}
        />
      );
    } else if (this.state.appStatus === 'loggingIn') {
      
      body = (
        <LoginPageBody
          appFunctions={this.appFunctions}
        />
      );
    } else if (this.state.appStatus === 'loginInProcess') {
      body = (
        <div><Spinner
          size={SpinnerSize.large}
        />
          <span style={{ alignSelf: 'center' }}>Login in progress.</span>
        </div>
      );
    } else if (this.state.appStatus === 'Waiting') {
      body = (
        <div><Spinner
          size={SpinnerSize.large}
        />
          <span style={{ alignSelf: 'center' }}>We are getting the data for you.</span>
        </div>
      );
    } else if (this.state.appStatus === 'LoggedIn' || this.state.appStatus === 'WaitingForData') {
      body = (
        <MainPageBody
          appFunctions={this.appFunctions}
        />
      );
    } else {
      if (this.state.tabSelected === 'notFetched') {
        console.log(
          `App Status is ${this.state.appStatus} and we went to the end`
        );
        body = <div> NotFetched and went to end </div>;
      } else if (this.state.tabSelected === 'fetchInProcess') {
        body = (
          <div><Spinner
            size={SpinnerSize.large}
          />
            <span>We are getting the data for you.</span>
          </div>
        );
      } else {
        body = (
          <SuccessPageBody logout={this.logout}/>
        );
      }
    }
    
    return (
      <Fabric className='App'>
        {this.state.errorMessage ? (
          <OfficeAddinMessageBar
            onDismiss={this.errorDismissed}
            message={this.state.errorMessage}
          />
        ) : null}
        <div className='ms-welcome'>
          <Header
            logo='assets/WSONE_icon_80x80px.jpg'
            title={this.props.title}
            message={this.state.headerMessage}
          />
          {body}
        </div>
      </Fabric>
    );
  }
}
