import {AxiosResponse} from 'axios';
import {AppState} from '../Components';

/*
     Interacting with the Office document
*/

const standardChartColors = {
  red: '#FD5B65',
  blue: '#2D99DA',
  green: '#0EBAB1',
  yellow: '#F7B731',
  deepblue: '#3867D6',
  grey: '#A5B1C2',
  purple: '#8755D0',
  charcoal: '#4B6483',
  pink: '#EE7482',
  cyan: '#70CBEB',
  fern: '#6BD6BB',
};

const riskChartColors = {
  ZERO: '#B7B7B7',
  LOW: '#5BC0DE',
  MEDIUM: '#FDB65B',
  HIGH: '#DA5F5C',
  VERY_HIGH: '#942622',
};

const standardChartColorsHashes = Object.values(standardChartColors);


export const createChart = async (ChartType: Number, sheetTitle: string, tableName: string, dataSize: [number, number], tableTags: string[]) => {
  switch (ChartType) {
    case 10:
      await createDoughnutChart(sheetTitle, tableName, dataSize, tableTags);
      break;
    case 20:
      await createBarChart(sheetTitle, tableName, dataSize, tableTags);
      break;
    case 60:
      await createStackedBarChart(sheetTitle, tableName, dataSize, tableTags);
      break;
    case 70:
      await createLinearChart(sheetTitle, tableName);
      break;
    default:
      break;
  }
};

export const createLinearChart = async (sheetTitle: string, tableName: string) => {
  await Excel.run(async (context: Excel.RequestContext) => {
    const sheet = context.workbook.worksheets.getItem(sheetTitle);
    const table = sheet.tables.getItem(tableName);
    const dataRange = table.getRange();

    let chart = sheet.charts.add(Excel.ChartType.lineMarkers, dataRange, "Auto");

    chart.setPosition("D1", "M21");
    chart.title.text = sheetTitle;
    chart.title.format.font.size = 14

    chart.legend.position = "Right";
    chart.legend.format.font.size = 14;

    chart.axes.categoryAxis.format.font.size = 14;
    chart.axes.valueAxis.format.font.size = 14;

    chart.series.getItemAt(0).format.line.color = standardChartColorsHashes[0];
    chart.series.getItemAt(0).markerForegroundColor = standardChartColorsHashes[0];
    chart.series.getItemAt(0).markerBackgroundColor = standardChartColorsHashes[0];

    await context.sync();

  });
}

export const createStackedBarChart = async (sheetTitle: string, tableName: string, dataSize: [number, number], tableTags: string[]) => {
  await Excel.run(async (context: Excel.RequestContext) => {

    const sheet = context.workbook.worksheets.getItem(sheetTitle);
    const table = sheet.tables.getItem(tableName);
    const dataRange = table.getRange();

    let chart = sheet.charts.add(Excel.ChartType.barStacked, dataRange, "Auto");
    chart.legend.overlay = false;

    chart.setPosition("A15", "I35");
    chart.title.text = sheetTitle;

    chart.axes.categoryAxis.format.font.size = 14;
    chart.axes.valueAxis.format.font.size = 14;

    chart.axes.categoryAxis.reversePlotOrder = true;
    chart.axes.valueAxis.tickLabelPosition = "High";
    chart.legend.visible = false;

    if (tableTags.length > 0) {
      for (var j = 0; j < dataSize[1]; j++) {
        let seriesCollection = chart.series.getItemAt(j);
        seriesCollection.gapWidth = 40;
        let points = seriesCollection.points;

        for (var i = 0; i < dataSize[0]; i++) {
          points.getItemAt(i).format.fill.setSolidColor(riskChartColors[tableTags[j]]);

        }
      }
    } else {
      for (var j = 0; j < dataSize[1]; j++) {
        let seriesCollection = chart.series.getItemAt(j);
        seriesCollection.gapWidth = 40;
        let points = seriesCollection.points;

        for (var i = 0; i < dataSize[0]; i++) {
          points.getItemAt(i).format.fill.setSolidColor(standardChartColorsHashes[j])
        }
      }
    }

    await context.sync();

  });
}

export const createBarChart = async (sheetTitle: string, tableName: string, dataSize: [number, number], tableTags: string[]) => {
  await Excel.run(async (context: Excel.RequestContext) => {
    const sheet = context.workbook.worksheets.getItem(sheetTitle);
    const table = sheet.tables.getItem(tableName);
    const dataRange = table.getDataBodyRange();

    let chart = sheet.charts.add(Excel.ChartType.barClustered, dataRange, "Auto");

    chart.setPosition("A15", "I35");
    chart.title.text = sheetTitle;
    chart.title.format.font.size = 14;

    chart.axes.valueAxis.tickLabelPosition = "High";

    chart.legend.visible = false;
    let seriesCollection = chart.series.getItemAt(0);
    seriesCollection.gapWidth = 40;

    chart.axes.categoryAxis.format.font.size = 14;
    chart.axes.valueAxis.format.font.size = 14;
    let points = seriesCollection.points;

    if (tableTags.length > 0) {
      for (var i = 0; i < dataSize[0]; i++) {
        points.getItemAt(i).format.fill.setSolidColor(riskChartColors[tableTags[i]]);
      }
    } else {
      chart.axes.categoryAxis.reversePlotOrder = true;
      for (var i = 0; i < dataSize[0]; i++) {
        points.getItemAt(i).format.fill.setSolidColor(standardChartColorsHashes[i])
      }
    }

    await context.sync();

  });
}

