const refreshSheet = async (context: Excel.RequestContext, result: AxiosResponse, sheetTitle: string, setState: (x: AppState) => void, displayError: (x: string) => void) => {
  const sheet = context.workbook.worksheets.getItem(sheetTitle);
  const tables = sheet.tables;

  tables.load("count");
  await context.sync();
  if (tables.count > 0) {
    const table = tables.getItemAt(0);
    table.rows.load('count');
    table.columns.load('count');

    await context.sync();
    const tableRowCount = table.rows.count;
    const tableColumnsCount = table.columns.count;

    const data = [];
    let tableTags: string[] = [];
    let isRiskData: boolean;
    let riskScores = Object.keys(riskChartColors);

    if (result.data.data !== null && result.data.data.length > 0) //two-column charts
    {
      if (result.data.data[0].id !== "null") {
        const id = [];
        for (var i = 0; i < result.data.data.length; i++) {
          id.push(result.data.data[i].id);
        }
        isRiskData = id.every(val => riskScores.includes(val))
          && result.data.groupBy.reportableType.entityType === "RISK";

        if (isRiskData) {
          riskScores.forEach(risk => {
            for (var i = 0; i < result.data.data.length; i++) {
              if (result.data.data[i].id === risk) {
                data.push([result.data.data[i].id, result.data.data[i].value]);
                tableTags.push(result.data.data[i].id);
              }
            }
          });
        } else {
          for (var i = 0; i < result.data.data.length; i++) {
            data.push([result.data.data[i].id, result.data.data[i].value]);
          }

        }
        //clean up the old table. columns should be handled similarly [TO DO]
        for (var d = 0; d < tableRowCount - data.length; d++) {
          table.rows.getItemAt(d).delete();
        }
        for (var d = 0; d < tableColumnsCount - data[0].length; d++) {
          table.columns.getItemAt(d).delete();
        }
        for (var d = 0; d < data[0].length - tableColumnsCount; d++) {
          table.columns.add();
        }
        table.getHeaderRowRange().values = [[result.data.primaryReportableType.entityType, "VALUE"]];
        sheet.getRange("A2:B" + (data.length + 1).toString()).values = data;
      } else { //single value
        data.push([result.data.data[0].value]);
        for (var d = 0; d < tableRowCount - data.length; d++) {
          table.rows.getItemAt(d).delete();
        }
        for (var d = 0; d < tableColumnsCount - data[0].length; d++) {
          table.columns.getItemAt(d).delete();
        }
        table.getHeaderRowRange().values = [[result.data.primaryReportableType.entityType]];
        sheet.getRange("A2").values = data;
      }
    } else if (result.data.datasets !== null && result.data.datasets.length > 0) {  //more than two columns or a timeseries
      const id = [];
      for (var i = 0; i < result.data.datasets.length; i++) {
        id.push(result.data.datasets[i].id);
      }

      let title = [];
      title.push(result.data.primaryReportableType.entityType);

      isRiskData = id.every(val => riskScores.includes(val))
        && result.data.groupBy.reportableType.entityType === "RISK";

      if (isRiskData) {
        let sortedDataset = [];
        riskScores.forEach(risk => {
          for (var i = 0; i < result.data.datasets.length; i++) {
            if (result.data.datasets[i].id === risk) {
              sortedDataset.push({id: result.data.datasets[i].id, data: result.data.datasets[i].data});
              tableTags.push(result.data.datasets[i].id);
            }
          }
        });
        for (var j = 0; j < sortedDataset[0].data.length; j++) {
          const data0 = [];
          data0.push(sortedDataset[0].data[j].id);
          for (var i = 0; i < sortedDataset.length; i++) {
            if (j === 0) {
              title.push(sortedDataset[i].id)
            }
            data0.push(sortedDataset[i].data[j].value);
          }
          data.push(data0);
        }
      } else {
        for (var j = 0; j < result.data.datasets[0].data.length; j++) {
          const data0 = [];
          data0.push(result.data.datasets[0].data[j].id);
          for (var i = 0; i < result.data.datasets.length; i++) {
            if (j === 0) {
              title.push(result.data.datasets[i].id)
            }
            ;
            data0.push(result.data.datasets[i].data[j].value);
          }
          data.push(data0);
        }

      }
      //clean up the old table. columns should be handled similarly [TO DO]
      for (var d = 0; d < tableRowCount - data.length; d++) {
        table.rows.getItemAt(d).delete();
      }
      for (var d = 0; d < tableColumnsCount - data[0].length; d++) {
        table.columns.getItemAt(d).delete();
      }
      for (var d = 0; d < data[0].length - tableColumnsCount; d++) {
        table.columns.add();
      }
      const alphabet = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
      alphabet[result.data.datasets.length];

      table.getHeaderRowRange().values = [title];
      sheet.getRange("A2:" + alphabet[result.data.datasets.length] + (data.length + 1).toString()).values = data;
    }
    if (data.length > 0) {
      sheet.activate();
      await context.sync();

      if (data[0].length > 1) // check if not single value
      {
        let dataSize: [number, number] = [data.length, data[0].length - 1]
        const charts = sheet.charts;

        charts.load("count");
        table.load("name");
        await context.sync();

        if (charts.count > 0) {
          const chart = sheet.charts.getItemAt(0);
          await updateChart(parseInt(result.data.type, 10), chart, dataSize, tableTags);
        } else {
          await createChart(parseInt(result.data.type, 10), sheetTitle, table.name, dataSize, tableTags);
        }
      }
    } else {
      displayError('Chart not found.');
      setState({tabSelected: 'notFetched'});
      sheet.activate();
      await context.sync();
    }
  } else {
    context.workbook.worksheets.getItemOrNullObject(sheetTitle).delete();
    await initializeSheet(context, result, sheetTitle, setState, displayError);
  }

};

const initializeSheet = async (context: Excel.RequestContext, result: AxiosResponse, sheetTitle: string, setState: (x: AppState) => void, displayError: (x: string) => void) => {
  context.workbook.worksheets.getItemOrNullObject(sheetTitle).delete();
  const sheet = context.workbook.worksheets.add(sheetTitle);
  let table: Excel.Table;

  const data = [];
  let tableTags: string[] = [];
  let isRiskData: boolean;
  let riskScores = Object.keys(riskChartColors);

  if (result.data.data !== null && result.data.data.length > 0) {
    if (result.data.data[0].id !== "null") {
      table = sheet.tables.add("A1:B1", true);
      table.name = "Table" + Math.random().toString(36).substr(2, 9);
      table.getHeaderRowRange().values = [[result.data.primaryReportableType.entityType, "VALUE"]];

      const id = [];
      for (var i = 0; i < result.data.data.length; i++) {
        id.push(result.data.data[i].id);
      }
      isRiskData = id.every(val => riskScores.includes(val))
        && result.data.groupBy.reportableType.entityType === "RISK";

      if (isRiskData) {
        riskScores.forEach(risk => {
          for (var i = 0; i < result.data.data.length; i++) {
            if (result.data.data[i].id === risk) {
              data.push([result.data.data[i].id, result.data.data[i].value]);
              tableTags.push(result.data.data[i].id);
            }
          }
        });
      } else {
        for (var i = 0; i < result.data.data.length; i++) {
          data.push([result.data.data[i].id, result.data.data[i].value]);
        }
      }
    } else //single value
    {
      table = sheet.tables.add("A1", true);
      table.name = "Table" + Math.random().toString(36).substr(2, 9);
      table.getHeaderRowRange().values = [[result.data.primaryReportableType.entityType]];
      data.push([result.data.data[0].value]);
    }
  } else if (result.data.datasets !== null && result.data.datasets.length > 0) {
    const alphabet = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
    table = sheet.tables.add("A1:" + alphabet[result.data.datasets.length] + "1", true);
    table.name = "Table" + Math.random().toString(36).substr(2, 9);

    let title = [];
    title.push(result.data.primaryReportableType.entityType);

    const id = [];
    for (var i = 0; i < result.data.datasets.length; i++) {
      id.push(result.data.datasets[i].id);
    }
    isRiskData = id.every(val => riskScores.includes(val))
      && result.data.groupBy.reportableType.entityType === "RISK";


    if (isRiskData) {
      let sortedDataset = [];
      riskScores.forEach(risk => {
        for (var i = 0; i < result.data.datasets.length; i++) {
          if (result.data.datasets[i].id === risk) {
            sortedDataset.push({id: result.data.datasets[i].id, data: result.data.datasets[i].data});
            tableTags.push(result.data.datasets[i].id);
          }
        }
      });
      for (var j = 0; j < sortedDataset[0].data.length; j++) {
        const data0 = [];
        data0.push(sortedDataset[0].data[j].id);
        for (var i = 0; i < sortedDataset.length; i++) {
          if (j === 0) {
            title.push(sortedDataset[i].id)
          }
          data0.push(sortedDataset[i].data[j].value);
        }
        data.push(data0);
      }
    } else {
      for (var j = 0; j < result.data.datasets[0].data.length; j++) {
        const data0 = [];
        data0.push(result.data.datasets[0].data[j].id);
        for (var i = 0; i < result.data.datasets.length; i++) {
          if (j === 0) {
            title.push(result.data.datasets[i].id)
          }
          ;
          data0.push(result.data.datasets[i].data[j].value);
        }
        data.push(data0);
      }
    }
    table.getHeaderRowRange().values = [title];
  }
  if (data.length > 0) {
    table.rows.add(null, data);
    sheet
      .getUsedRange()
      .getEntireColumn()
      .format.autofitColumns();
    sheet
      .getUsedRange()
      .getEntireRow()
      .format.autofitRows();
    sheet.activate();
    await context.sync();

    let dataSize: [number, number] = [data.length, data[0].length - 1]
    await createChart(parseInt(result.data.type, 10), sheetTitle, table.name, dataSize, tableTags);
  } else {
    displayError('Chart not found.');
    setState({tabSelected: 'notFetched'});
    sheet.activate();
    await context.sync();
  }

};

export const ProcessData = async (result: AxiosResponse, setState: (x: AppState) => void,
                                  displayError: (x: string) => void,) => {
  await Excel.run(async (context: Excel.RequestContext) => {
    const sheetTitle = result.data.name.toString().substring(0, 31);
    var worksheet = context.workbook.worksheets.getItemOrNullObject(sheetTitle);

    await context.sync();
    if (!worksheet.isNullObject) //updating an exising chart
    {
      await refreshSheet(context, result, sheetTitle, setState, displayError);
    } else //creating new a new chart
    {
      await initializeSheet(context, result, sheetTitle, setState, displayError);
    }
  })
    .catch((error) => {
      displayError(error.toString());
    });
};

