export const getColumnLetters = () => {
  const columnLetters: string[] = [];
  const alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
  for (let x = 0; x < alphabet.length; x++) {
    for (let y = 0; y < alphabet.length; y++) {
      if (x === 0) {
        columnLetters.push(alphabet[y]);
      } else {
        columnLetters.push(alphabet[x - 1] + alphabet[y]);
      }
    }
  }
  return columnLetters;
};

export const loadObjectValFromArray = async (fieldToSearch: string, value, myArray): Promise<{}> => {
  let blockingSince: number = Date.now();
  let fields = fieldToSearch.split('.');
  // console.log(fields);
  // console.log('value' + value);
  // console.log(myArray);
  // myArray = myArray.map(array => {
  //   return array.then(array => {
  //     return array;
  //   });
  // });
  if (fields.length === 2) {
    for (let i = 0; i < (await myArray.length); i++) {
      let now: number = Date.now();
      if (now - blockingSince > 10) {
        await new Promise(resolve => setImmediate(resolve));
        blockingSince = now;
      }
      if ((await myArray[i][fields[0]][fields[1]]) === value) {
        // console.log(myArray[i].attributes.data);
        
        return await myArray[i];
      }
    }
  } else {
    for (let i = 0; i < (await myArray.length); i++) {
      let now: number = Date.now();
      if (now - blockingSince > 10) {
        await new Promise(resolve => setImmediate(resolve));
        blockingSince = now;
      }
      if ((await myArray[i][fieldToSearch]) === value) {
        return await myArray[i];
      }
    }
  }
  return 'NotFound';
};

export const getCurrentTab = async () => {
  let tabListTypes = ['Devices', 'Apps', 'Profiles', 'Baselines'];
  let tabObjectTypes = ['Device_', 'App_', 'Profile_', 'Baseline_'];
  let sheetName: string;
  let returnArray: any[] = [];
  
  await Excel.run(async (context) => {
    var sheet = await context.workbook.worksheets.getActiveWorksheet();
    await sheet.load('name');
    await context.sync();
    
    sheetName = await sheet.name;
    // console.log(sheetName.substr(0, sheetName.indexOf('_') + 1));
    if (tabListTypes.includes(sheetName)) {
      console.log(`Error: You must be on an individual
          ${sheet.name} to add additional data.',
      `);
      await returnArray.push('none', '0');
    } else if (
      tabObjectTypes.includes(sheetName.substr(0, sheetName.indexOf('_') + 1))
    ) {
      await returnArray.push(
        sheetName.substr(0, sheetName.indexOf('_')),
        sheetName.substr(
          sheetName.indexOf('_') + 1,
          (sheetName.indexOf('_', sheetName.indexOf('_') + 1) - sheetName.indexOf('_') - 1) > 1 ? (sheetName.indexOf('_', sheetName.indexOf('_') + 1) - sheetName.indexOf('_') - 1) : (sheetName.indexOf('_') - 1 || sheetName.length - sheetName.indexOf('_') + 1)
        ),
        sheetName.substr(sheetName.indexOf('_', sheetName.indexOf('_') + 1) + 1, sheetName.length)
      );
    } else {
      await returnArray.push('none', '0');
    }
    await returnArray.push(sheetName);
  }).catch((error) => {
    console.log(error);
  });
  
  return returnArray;
};

// Get an array element in column/row order
var getArray2d = function(a, x, y) {
  return a[y][x];
};


export function rotateArray(arr) {
  var newarr = [];
  arr[0].forEach(() => newarr.push(new Array(arr.length)));
  
  for (var i = 0; i < newarr.length; i++) {
    for (var j = 0; j < newarr[0].length; j++) {
      newarr[i][j] = getArray2d(arr, i, j);
    }
  }
  return newarr;
}
