const refreshSheetAsTable = async (
  context: Excel.RequestContext,
  boostedFields: string[],
  dataType: string,
  columnLetters: string[],
  header: any[],
  data: [][],
  sheetTitle: string,
  addOnDataType: string,
  startingRow: number,
  displayError: (x: string) => void
) => {
  OfficeExtension.config.extendedErrorLogging = true;

  const sheet = context.workbook.worksheets.getItem(sheetTitle);
  sheet.load().activate();
  let table: Excel.Table;
  let tableExists = false;
  let tableList = sheet.tables.load();
  await context.sync();
  // console.log(tableList);

  if (addOnDataType.length > 0) {
    table = sheet.tables.getItemOrNullObject(
      sheetTitle + 'Table' + addOnDataType
    );
    await context.sync();
    if (table.isNullObject) {
      let totalRows = startingRow + data[0].length + 2;
      var row = sheet.getRange('A' + startingRow + ':ZZ' + totalRows);
      row.insert(Excel.InsertShiftDirection.down);
      await context.sync();
      table = sheet.tables.add(
        'A' +
        startingRow +
        ':' +
        columnLetters[header[0].length - 1] +
        startingRow,
        true
      );
      table.name = sheetTitle + 'Table' + addOnDataType;
      table.getHeaderRowRange().values = header;
      table.rows.add(null, data);
    } else {
      table = sheet.tables.getItem(sheetTitle + 'Table' + addOnDataType);
      tableExists = true;
    }
  } else {
    if (tableList.count > 1) {
      displayError('Excel does not allow manipulation of higher row tables ( eg. The top table) when there are more than one tables on a sheet.  You can refresh data on the lowest table only.');
      return;
    }

    table = sheet.tables.getItem(sheetTitle + 'Table');
    await context.sync();
    tableExists = true;
  }

  if (tableExists) {
    table.load();
    table.rows.load();
    table.columns.load();

    await context.sync();
    const tableRowCount = table.rows.count;
    const tableColumnsCount = table.columns.count;

    if (tableColumnsCount < header[0].length) {
      for (let i = 0; i < header[0].length - tableColumnsCount; i++) {
        table.columns.add();
      }
    } else if (tableColumnsCount > header[0].length) {
      // console.log(tableColumnsCount);
      // console.log(header[0].length);
      for (let i = 0; i < tableColumnsCount - header[0].length; i++) {
        table.columns.getItemAt(0).delete();
      }
    }
    table.getHeaderRowRange().values = header;
    await context.sync();

    if (tableRowCount) {
      for (let d = tableRowCount - 1; d > -1; d--) {
        table.rows.getItemAt(d).delete();
      }
      table.rows.add(null, data);
    } else {
      table.rows.add(0, data);

    }
    await context.sync();
  }

  sheet.getUsedRange().getEntireColumn().format.autofitColumns();
  sheet.getUsedRange().getEntireRow().format.autofitRows();
  sheet.activate();

  if (dataType === 'Object') {
    let tempTitleArray = [];
    let tempValueArray = [];
    for (let i = 0; i < boostedFields.length; i++) {
      let sourceTitleCell = sheet.getCell(0, i);
      sourceTitleCell.load('values');
      tempTitleArray.push(sourceTitleCell);
      let sourceValueCell = sheet.getCell(1, i);
      sourceValueCell.load('values');
      tempValueArray.push(sourceValueCell);
    }

    await context.sync();
    for (let i = 5; i < boostedFields.length + 5; i++) {
      let cell = sheet.getRange('A' + i);
      cell.values = tempTitleArray[i - 5].values;

      let value = sheet.getRange('B' + i);
      value.values = tempValueArray[i - 5].values;
    }
    let type = sheetTitle.split('_')[0];
    let cell = sheet.getRange('A4');
    cell.values = [[type + ' Details']];
    cell.format.autofitColumns();
  }

  await context.sync();
};
export default refreshSheetAsTable;
