export const getDeviceApps = async (
  deviceId,
  authParams: AuthParams,
  appFunctions: AppFunctions, setLocalState) => {
  
  let path = ['mdm', 'devices', deviceId, 'apps'];
  let queryParams = { pagesize: 5000 };
  let apiReturnType: ApiReturnType = 'Array';
  let apiReturnValue = 'DeviceApps';
  let apiBoostedFields = ['Id:Value', 'ApplicationName', 'AppType'];
  let localOnly = true;
  let sheetTitle = '';
  let addOnDataType = '';
  let listName = 'deviceAppsList';
  let identifier = deviceId;
  let updateLocalState = false;
  //dfsdff
  return await genericListFunc(
    authParams,
    path,
    queryParams,
    apiReturnType,
    apiReturnValue,
    apiBoostedFields,
    localOnly,
    sheetTitle,
    addOnDataType,
    listName,
    identifier,
    updateLocalState,
    appFunctions,
    setLocalState
  );
};


