import {ApiParams, QueryParams, AuthParams, ApiReturnType} from '../Components';
import {ApiModel, Attributes, Eventing} from '../Models';

// AdminsApi is used to get the initial login information for users when they login and find out
// their user information and which roles they have in the environment
export class GenericListApiProps {
  constructor(
    public apiReturnType: ApiReturnType,
    public authParams: AuthParams,
    public apiParams?: ApiParams,
    public queryParams?: QueryParams,
  ) {
  }
}

export class GenericListApi extends ApiModel<GenericListApiProps> {
  static path = [];
  static apiReturnType = 'Array';
  static apiReturnValue = '';
  static apiBoostedFields = [];

  static async buildObject(attrs: GenericListApiProps): Promise<GenericListApi> {
    return new GenericListApi(
      new Attributes<GenericListApiProps>(attrs),
      new Eventing()
    );
  }
}
