import * as React from 'react';
import {
  DefaultButton,
  ButtonType, MessageBar, SpinnerSize, Spinner /*, TextField, Text */
} from '@fluentui/react';

import { ApiParams, MainPageBodyProps } from '../Components';
import {
  GenericListApi,
  GenericListApiProps,
  SingleObjectApi,
  SingleObjectApiProps
} from '../Apis';
import { ApiCollection } from '../Models';

import {
  buildApiArray, convertApiDataToSheetAsRange,
  getCurrentTab,
  // getCurrentTab,
  sortArray
} from '../Utilities';
import { Stack } from 'office-ui-fabric-react/lib/Stack';
import { getBaselinesList, getGroupDetails } from './CommonFunctions';

export interface BaselinesPageBodyState {
  listStatus?: {};
}

export class BaselinesPageBody extends React.Component<MainPageBodyProps,
  BaselinesPageBodyState> {
  constructor(props) {
    super(props);
    this.state = {
      listStatus: {}
      
    };
    
    this.handleChangeGroupId = this.handleChangeGroupId.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  handleSubmit(event) {
    event.preventDefault();
  }
  
  handleChangeGroupId(event) {
    this.props.appFunctions.setAppState({ groupid: event.target.value });
  }
  
  
  getBaseline = async (baselineId: any, authParams, appFunctions) => {
    // if (!this.groupDetails) {
    //   await this.getGroupDetails(authParams, setAppState, getStateData);
    // }
    let apiParams: ApiParams = {
      authParams: authParams,
      queryParams: {},
      path: [
        'mdm',
        'groups',
        (await getGroupDetails(authParams, 'true', appFunctions))['Uuid'],
        'baselines',
        baselineId
      ],
      apiReturnType: 'Object',
      apiReturnValue: '',
      apiBoostedFields: ['baselineUUID']
    };
    
    const baseline = new ApiCollection(
      apiParams,
      async (json: SingleObjectApiProps) => {
        return await SingleObjectApi.buildObject(json);
      }
    );
    
    await baseline.on('change', async () => {
      await baseline.models[0].then(async (result) => {
        await convertApiDataToSheetAsRange(
          sortArray(
            await buildApiArray(result['attributes']['data'], this.props.appFunctions.addTimerList),
            baseline.apiParams.apiBoostedFields, true
          ),
          baseline.apiParams.apiBoostedFields,
          'Object',
          'Baseline_' + result['attributes']['data'].name,
          '',
          appFunctions.setAppState
        );
      });
    });
    await baseline.fetch(appFunctions);
  };
  
  getSelectedBaseline = async (authParams, appFunctions) => {
    
    if (appFunctions.getStateData('listExists', 'baselinesList')) {
      let baselinesKeys = Object.keys(appFunctions.getStateData('list', 'baselinesList'));
      if (baselinesKeys.length === 0) {
        await getBaselinesList(false, authParams, appFunctions, this.setLocalState);
      }
    }
    
    await Excel.run(async (context) => {
      let range = await context.workbook.getSelectedRange();
      range.load('address');
      range.load('values');
      return await context.sync().then(async () => {
        range.values.map(async (value) => {
          if (value[0]) {
            await this.getBaseline(value[0], authParams, appFunctions);
          }
        });
      });
    }).catch((e) => {
      appFunctions.setAppState({ errorMessage: e.toString() });
    });
  };
  
  getCurrentBaselineDevices = async (authParams, appFunctions) => {
    let currentTab = await getCurrentTab();
    let baselineUuid = '';
    
    if (currentTab[0] === 'Baseline') {
      await Excel.run(async (context) => {
        let sheet = context.workbook.worksheets.getActiveWorksheet();
        let range = sheet.getRange('A2');
        range.load('values');
        return await context.sync().then(async () => {
          return range.values.map(async (value) => {
            baselineUuid = value[0];
          });
        });
      }).catch((e) => {
        appFunctions.setAppState({ errorMessage: e.toString() });
      });
      let result;
      if (appFunctions.getStateData('listExists', 'baselineDevices')) {
        result = appFunctions.getListValue('baselineDevices', 'identifier', baselineUuid);
      }
      
      let apiParams: ApiParams = {
        authParams: authParams,
        queryParams: { max_results: 100000 },
        path: [
          'mdm',
          'groups',
          (await getGroupDetails(authParams, 'true', appFunctions))['Uuid'],
          'baselines',
          baselineUuid,
          'devices'
        ],
        apiReturnType: 'Array',
        apiReturnValue: 'results',
        apiBoostedFields: [
          'deviceID',
          'friendlyName',
          'status:status',
          'status:errorCode',
          'status:version',
          'status:reportedOn',
          'status:errorMessage',
          'compliance:status',
          'compliance:level'
        ]
      };
      
      if (result && result.length === 1) {
        await convertApiDataToSheetAsRange(
          sortArray(
            await buildApiArray(result[0].list, this.props.appFunctions.addTimerList),
            apiParams.apiBoostedFields, true
          ),
          apiParams.apiBoostedFields,
          'Array',
          currentTab[3],
          'Devices',
          appFunctions.setAppState
        );
      } else {
        const baselineDevices = new ApiCollection(
          apiParams,
          async (json: GenericListApiProps) => {
            return await GenericListApi.buildObject(json);
          }
        );
        
        await baselineDevices.on('change', async () => {
          if (baselineDevices) {
            let models = [];
            await baselineDevices.models.forEach((object) => {
              object.then((result) => {
                models.push(result.attributes['data']);
              });
            });
            appFunctions.addToAllLists('baselineDevices', { identifier: baselineUuid, list: models });
            
            let tempArray = sortArray(
              await buildApiArray(models, appFunctions.addTimerList),
              apiParams.apiBoostedFields, true
            );
            
            await convertApiDataToSheetAsRange(
              tempArray,
              apiParams.apiBoostedFields,
              'Array',
              currentTab[3],
              'Devices',
              appFunctions.setAppState
            );
          }
        });
        
        await baselineDevices.fetch(appFunctions);
      }
    }
  };
  
  setLocalState = async (newStateData: {}) => {
    this.setState(newStateData);
  };
  
  render() {
    const { appFunctions } = this.props;
    const authParams = {
      url: appFunctions.getStateData('stateVal', 'url'),
      adminName: appFunctions.getStateData('stateVal', 'adminName'),
      password: appFunctions.getStateData('stateVal', 'password'),
      apikey: appFunctions.getStateData('stateVal', 'apikey'),
      domain: appFunctions.getStateData('stateVal', 'domain')
    };
    // const textvariants = [
    //   { name: 'tiny' },
    //   { name: 'xSmall' },
    //   { name: 'small' },
    //   { name: 'smallPlus' },
    //   { name: 'medium' },
    //   { name: 'mediumPlus' },
    //   { name: 'large' },
    //   { name: 'xLarge' },
    //   { name: 'xxLarge' },
    //   { name: 'mega' },
    // ];
    let getBaselinesListBody, getCurrentBaselineDevicesStatusBody;
    if (this.state.listStatus['baselinesList'] && JSON.stringify(this.state.listStatus['baselinesList']) !== '{}') {
      getBaselinesListBody = (
        <MessageBar>
          Getting {this.state.listStatus['baselinesList'] && JSON.stringify(this.state.listStatus['baselinesList']) !== '{}' ? this.state.listStatus['baselinesList']['value']['currentPage'] + ' page of ' : null}
          of {this.state.listStatus['baselinesList'] && JSON.stringify(this.state.listStatus['baselinesList']) !== '{}' ? this.state.listStatus['baselinesList']['value']['totalPages'] + ' pages.' : null}
        </MessageBar>
      );
    } else {
      getBaselinesListBody = null;
    }
    
    if (this.state.listStatus['currentBaselineDevices'] && JSON.stringify(this.state.listStatus['currentBaselineDevices']) !== '{}') {
      getCurrentBaselineDevicesStatusBody = (
        <MessageBar>
          Getting Current Baseline Devices.
          <Spinner
            size={SpinnerSize.xSmall}
          />
        </MessageBar>
      );
    } else {
      getCurrentBaselineDevicesStatusBody = null;
    }
    
    
    const stackTokens = { childrenGap: 5, maxWidth: 300 };
    return (
      <Stack tokens={stackTokens}>
        <br/>
        <Stack.Item align='center'>
          <h3>Baselines Page</h3>
        </Stack.Item>
        <p>
          Choose to get a list of your Baselines or select a Baseline Id on your
          list and get various details about your Baseline.
        </p>
        <Stack.Item align='center'>
          <DefaultButton
            type='submit'
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={() => {
              setTimeout(() => {
                this.setState({ listStatus: { ['baselinesList']: this.props.appFunctions.getStateData('value', 'baselinesList') } });
              }, 100);
              getBaselinesList(false, authParams, appFunctions, this.setLocalState).then(() => {
                  this.setState({ listStatus: { ['baselinesList']: this.props.appFunctions.getStateData('value', 'baselinesList') } });
                }
              );
              
            }
            }
          >
            Get Baselines List
          </DefaultButton>
          {getBaselinesListBody}
        </Stack.Item>
        <Stack.Item align='center'>
          <DefaultButton
            type='submit'
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={() => this.getSelectedBaseline(authParams, appFunctions)}
          >
            Get Selected Baseline(s) Data
          </DefaultButton>
        </Stack.Item>
        <Stack.Item align='center'>
          <DefaultButton
            type='submit'
            className='ms-welcome__action'
            buttonType={ButtonType.hero}
            iconProps={{ iconName: 'ChevronRight' }}
            onClick={() => {
              setTimeout(() => {
                this.setState({ listStatus: { ['currentBaselineDevices']: { test: 100 } } });
              }, 100);
              this.getCurrentBaselineDevices(authParams, appFunctions).then(() => {
                  setTimeout(() => {
                    this.setState({ listStatus: { ['currentBaselineDevices']: {} } });
                  }, 2000);
                  
                }
              );
              
            }
            }
          >
            Get Current Baselines Devices
          </DefaultButton>
          {getCurrentBaselineDevicesStatusBody}
        </Stack.Item>
      </Stack>
    );
  }
}
