import axios, {AxiosPromise} from 'axios';
import {ApiParams} from '../Components';
import * as https from 'https';

const httpsAgent = new https.Agent({keepAlive: true});

const getBasicAuth = (username: string, password: string) => {
  let buff = new Buffer(username + ':' + password);
  return buff.toString('base64');
};

const serialize = (obj) => {
  var str = [];
  for (var p in obj)
    if (obj.hasOwnProperty(p)) {
      str.push(encodeURIComponent(p) + '=' + encodeURIComponent(obj[p]));
    }
  return str.join('&');
};

export class Sync {
  constructor() {
  }

  fetch(apiParams: ApiParams): AxiosPromise {
    return axios({
      url: apiParams.authParams.url + serialize(apiParams.queryParams),
      method: 'get',
      httpsAgent: httpsAgent,
      headers: {
        'Content-Type': `application/x-www-form-urlencoded`,
        'Authorization': 'Basic ' + getBasicAuth(apiParams.authParams.adminName, apiParams.authParams.password),
        'Accept': 'application/json;version=2;',
        'aw-tenant-code': apiParams.authParams.apikey,
        'domain': apiParams.authParams.domain,
        'path': apiParams.path,
      }
    });
  }

}
