import {
  ApiParams,
  QueryParams,
  AuthParams,
  ApiReturnType,
} from '../Components';
import {ApiModel, Attributes, Eventing} from '../Models';

// GroupApi is used to get group details and uuid to use for other queries
export class SingleObjectApiProps {
  constructor(
    public apiReturnType: ApiReturnType,
    public authParams: AuthParams,
    public apiParams?: ApiParams,
    public queryParams?: QueryParams
  ) {
  }
}

export class SingleObjectApi extends ApiModel<SingleObjectApiProps> {
  static path = [];
  static apiReturnType = 'Object';
  static apiReturnValue = '';
  static apiBoostedFields = [];

  static async buildObject(attrs: SingleObjectApiProps): Promise<SingleObjectApi> {
    return new SingleObjectApi(
      new Attributes<SingleObjectApiProps>(attrs),
      new Eventing()
    );
  }
}
